.review-text {
  position: relative;
  background: $c-substrate;
  border-radius: 15px;
  padding: 30px 10px 30px 30px;

  @include xl {
    padding: 20px;
  }

  @include xs {
    padding: 22px 10px 30px 20px;
  }

  &__content {
    font-size: 26px;
    margin: 0 0 30px;

    @include xl {
      font-size: 20px;
    }

    @include xs {
      font-size: 16px;
    }
  }

  &__popup.admin-popup {
    position: absolute;
    right: 15px;
    bottom: 5px;
  }
}
