.course-hero {
  position: relative;
  z-index: 1;
  min-height: 740px;
  color: $c-white;
  background: $c-accent;
  padding: 160px 0 64px;

  @include xl {
    min-height: 670px;
    padding: 192px 0 90px;
  }

  @include lg {
    min-height: auto;
  }

  @include md {
    padding: 140px 0 50px;
  }

  @include xs {
    padding: 90px 0 20px;
  }

  &__bg {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
    width: 848px;
    height: 739px;

    @include lg {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__wrap {
    max-width: 661px;
    width: 100%;
  }

  &__title {
    @include title-h1;
    margin: 0 0 15px;
  }

  &__subtitle {
    max-width: 470px;
    width: 100%;
    margin: 0 0 30px;

    @include xl {
      margin: 0 0 45px;
    }

    @include xs {
      padding: 0 40px 0 0;
    }
  }

  &__features.course-features {
    max-width: 480px;
    width: 100%;
    margin: 0 0 30px;

    @include xl {
      max-width: 355px;
      margin: 0 0 45px;
    }

    .course-features__item {
      border-color: $c-white;
    }

    .course-features__item::before {
      background: $c-white;
    }
  }

  &__btn {
    min-width: 470px;
    color: $c-text;

    @include xl {
      min-width: 350px;
      min-height: 60px;
    }

    @include xs {
      min-width: 100%;
    }
  }
}
