.admin-popup {
  position: relative;
  width: 24px;
  height: 24px;

  &:hover {
    .admin-popup__content {
      opacity: 1;
      visibility: visible;
      pointer-events: all;
    }
  }

  &__content {
    width: 176px;
    position: absolute;
    top: calc(100% - 6px);
    right: 0;
    z-index: 1;
    background: $c-white;
    box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
    border-radius: 5px;
    padding: 20px 15px;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    transition: visibility .3s, opacity .3s;

    &::before {
      content: '';
      width: calc(100% - 24px);
      height: 10px;
      background: red;
      background: transparent;
      position: absolute;
      bottom: 100%;
      left: 0;
    }

    @include xl {
      width: 187px;
      padding: 25px 20px;
    }
  }

  &__btn {
    width: 100%;
    height: 100%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }

  &__link {
    display: flex;
    align-items: center;
    font-size: 16px;
    color: $c-text;
    transition: color .3s;

    &:not(:last-of-type) {
      margin: 0 0 15px;

      @include xl {
        margin: 0 0 20px;
      }
    }

    &:hover {
      color: $c-accent;

      .admin-popup__link-icon {
        stroke: $c-accent;
      }
    }
  }

  &__link-top {
    color: $c-accent;
    border-bottom: 1px solid $c-border;
    padding: 0 0 15px;

    @include xl {
      padding: 0;
      border-bottom: none;
    }

    &:hover {
      color: $c-text;
    }
  }

  &__link-icon {
    width: 18px;
    height: 18px;
    flex-shrink: 0;
    margin: 0 15px 0 0;
    stroke: $c-text;
    fill: transparent;
    transition: stroke .3s;
  }
}
