.tasks-cabinet {
  margin: -160px 0 0;
  padding: 0 0 110px;

  @include xl {
    margin: -135px 0 0;
    padding: 0 0 90px;
  }

  @include xs {
    margin: -150px 0 0;
    padding: 0 0 60px;
  }

  .more {
    display: none;

    @include xs {
      display: block;
    }
  }

  &__tags {
    margin: 0 0 25px;

    .tags__item {
      &:hover {
        border-color: $c-text;
      }
    }

    .tags__item.active {
      border-color: $c-text;
    }

    @include xs {
      margin: 0 0 10px;
    }
  }

  &__wrap {}

  &__item {
    display: block;
    background: $c-white;
    border: 1px solid $c-border;
    box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
    border-radius: 15px;
    padding: 30px;

    @include xl {
      padding: 15px 12px 15px 15px;
    }

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }
  }

  &__item-tags {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 10px;
  }

  &__tags-item {
    font-size: 14px;
    color: $c-text;
    background: $c-secondary50;
    border-radius: 4px;
    padding: 7px 5px;
    margin: 0 15px 5px 0;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      margin: 0 5px 5px 0;
    }

    &--red {
      background: $c-accent;
      color: $c-white;
    }

    &--blue {
      background: $c-purple;
      color: $c-white;
    }
  }


  &__item-name {
    display: inline-block;
    font-size: 20px;
    font-weight: 500;
    color: $c-text;
    margin: 0 0 15px;
    transition: color .3s;

    @include xs {
      font-size: 18px;
    }

    &:hover {
      color: $c-accent;
    }
  }

  &__item-experience {
    display: flex;
    align-items: center;
    font-size: 14px;

    @include xs {
      font-size: 12px;
    }
  }

  &__item-experience-star {
    width: 18px;
    height: 18px;
    margin: 0 5px 0 0;
    flex-shrink: 0;
    object-fit: cover;
  }
}
