.admin-reviews {

  &__filter {
    position: relative;
    padding: 5px 0;

    &:hover {
      .admin-reviews__filter-popup {
        opacity: 1;
        visibility: visible;
        pointer-events: all;
      }
    }
  }

  &__filter-btn {
    min-width: auto;
    font-weight: 400;
    font-size: 16px;
    color: $c-label;
    padding: 0 30px;
    border: 1px solid $c-border;

    @include xl {
      min-width: auto;
      min-height: 45px;
    }

    svg {
      fill: $c-label;
      flex-shrink: 0;
      width: 20px;
      height: 20px;
      margin: 3px 0 0 25px;
      transition: fill .3s;
    }
  }

  &__filter-popup {
    width: 100%;
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 1;
    background: $c-white;
    box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
    border-radius: 5px;
    padding: 20px 15px;
    visibility: hidden;
    opacity: 0;
    pointer-events: none;
    transition: visibility .3s, opacity .3s;
  }

  &__filter-popup-item {
    display: block;

    .checkbox__el::after {
      width: 100%;
      height: 100%;
    }

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }
  }
}
