.content-course {
  border: 1px solid $c-border;
  border-radius: 5px;
  padding: 30px 60px 24px 30px;

  @include xl {
    padding: 30px 10px 60px 30px;
  }

  &__descr {
    margin: 0 0 30px;

    p {
      &:not(:last-of-type) {
        margin: 0 0 20px;
      }
    }

    @include xl {
      font-size: 14px;
    }
  }

  &__list {
    li {
      display: flex;

      @include xl {
        font-size: 14px;
      }

      &::before {
        content: '';
        width: 16px;
        height: 16px;
        background: url('../img/svg/arrow-right-circle.svg') no-repeat center/cover;
        flex-shrink: 0;
        margin: 3px 10px 0 0;

        @include xl {
          margin: -1px 10px 0 0;
        }
      }

      &:not(:last-of-type) {
        margin: 0 0 25px;
      }
    }
  }
}
