// Default


// Color

$c-white:                       #FFF;
$c-accent:                      #FA4242;
$c-secondary:                   #FFC85B;
$c-secondary50:                 rgba(253, 170, 4, 0.5);
$c-purple:                      #631FF4;
$c-green:                       #0CCD87;
$c-dark:                        #212121;
$c-text:                        #353535;
$c-border:                      #DDD;
$c-substrate:                   #F7F7F7;
$c-label:                       #727272;

// Typography
//---------------------------------

$font-family-roboto:             "Roboto", sans-serif;
$font-family-lato:               "Lato", sans-serif;
$font-family-alegreya:           "Alegreya-Sans", sans-serif;

// Animation
//---------------------------------

$default-transition-settings:       0.3s ease;
$modal-transition-settings:         0.6s cubic-bezier(0.55, 0, 0.1, 1);
// Viewports
//---------------------------------

$viewport--xs:                      575px;
$viewport--sm:                      768px;
$viewport--md:                      992px;
$viewport--lg:                      1200px;
$viewport--xl:                      1441px;
$viewport--xxl:                     1920px;

// Retina
//---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
