.promotion {
  position: relative;
  z-index: 1;
  background: $c-secondary;

  @include xs {
    min-height: 550px;
    display: flex;
    flex-direction: column;
  }

  &__name {
    max-width: 926px;
    width: 100%;
    font-weight: 500;
    font-size: 72px;
    margin: 0 0 30px;

    @include xl {
      max-width: 638px;
      font-size: 50px;
    }

    @include md {
      font-size: 42px;
    }

    @include xs {
      font-size: 30px;
    }
  }

  &__features.course-features {
    max-width: 478px;
    margin: 0 0 20px;

    .course-features__item {
      border: 1px solid rgba(53, 53, 53, 0.5);
    }

    @include xl {
      max-width: 345px;
      margin: 0 0 30px;
    }
  }

  &__btn {
    min-height: 90px;

    @include xl {
      min-height: 60px;
    }

    @include xs {
      min-width: 100%;
      margin: auto 0 0;
    }
  }

  &__bg {
    position: absolute;
    top: 0;
    right: 0;
    width: 49%;
    height: 100%;
    z-index: -1;

    @include xs {
      width: auto;
      height: auto;
      top: auto;
      bottom: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__bg-img {
    @media (max-width: 1250px) {
      display: none;
    }
  }

  &__bg-img-mob {
    display: none;

    @include xs {
      display: block;
    }
  }
}
