.about-features {

  &__head {}

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 -30px;

    @include xl {
      margin: 0 0 -30px;
    }

    @include lg {
      justify-content: space-between;
      margin: 0 0 -20px;
    }
  }

  &__item {
    position: relative;
    z-index: 1;
    min-height: 456px;
    width: calc(25% - 30px * 3 / 4);
    margin: 0 30px 30px 0;
    border-radius: 25px;
    padding: 30px;
    overflow: hidden;

    @include xl {
      min-height: 350px;
      width: calc(25% - 15px * 3 / 4);
      margin: 0 15px 15px 0;
    }

    @include lg {
      width: calc(50% - 15px / 2);
      margin: 0 0 20px;
    }

    @include sm {
      min-height: 380px;
      width: 100%;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }

    &--1 {
      background: #D2E3FB;
      color: #3D7EE8;
    }

    &--2 {
      background: $c-secondary;
      color: $c-text;
    }

    &--3 {
      background: #EBDDFB;
      color: #A258F5;

      @include xl {
        .about-features__img {
          max-width: 210px;
          right: -60px;
        }
      }

      @include sm {
        .about-features__img {
          max-width: 270px;
          right: 10px;
        }
      }
    }

    &--4 {
      background: #D2FBF3;
      color: #0ABC99;
    }
  }


  &__name {
    font-weight: 700;
    font-size: 24px;
    margin: 0 0 15px;
  }

  &__descr {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;

    @include xs {
      font-size: 18px;
    }
  }

  &__img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
    max-height: 270px;
    object-fit: cover;

    @include xl {
      max-height: 180px;
      right: -20px;
    }

    @include sm {
      max-height: 220px;
      right: -5px;
    }
  }
}
