.info-cabinet {
  display: flex;
  padding: 0 0 120px;

  @include xl {
    padding: 0 0 105px;
  }

  @include lg {
    display: block;
  }

  @include xs {
    padding: 0 0 60px;
  }

  .more {
    display: none;

    @include xs {
      display: block;
    }
  }

  &__item {
    background: $c-white;
    border: 1px solid $c-border;
    box-shadow: 0px 8px 24px rgba(25, 32, 56, 0.04);
    border-radius: 15px;
    padding: 30px;

    @include md {
      padding: 20px;
    }

    @include xs {
      padding: 15px;
    }
  }

  &__profile {
    flex: 0 1 50%;
    margin: 0 40px 0 0;

    @include xl {
      flex: 0 1 550px;
      margin: 0 30px 0 0;
    }

    @include lg {
      margin: 0 0 30px;
    }
  }

  &__profile-head {
    display: flex;
    align-items: center;
    margin: 0 0 45px;

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__profile-photo {
    position: relative;
    width: 121px;
    height: 121px;
    border-radius: 50%;
    flex-shrink: 0;
    margin: 0 30px 0 0;
    cursor: pointer;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background: linear-gradient(0deg, rgba(53, 53, 53, 0.6), rgba(53, 53, 53, 0.6));
    }
  }

  &__profile-photo-img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%;
    filter: blur(5px);
  }

  &__profile-photo-icon {
    width: 24px;
    height: 24px;
    object-fit: cover;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  &__profile-head-info {}

  &__profile-name {
    font-weight: 500;
    font-size: 70px;

    @include xl {
      font-size: 40px;
    }

    @include lg {
      font-size: 32px;
    }

    @include md {
      font-size: 26px;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__profile-experience {
    font-size: 18px;

    @include xs {
      font-size: 12px;
    }
  }

  &__profile-experience-val {
    color: $c-secondary;
  }

  &__profile-label {
    margin: 0 0 45px;

    @include xl {
      .label__name {
        font-size: 12px;
      }

      .input {
        height: 45px;
        font-size: 14px;
      }
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__profile-btn {
    min-width: 100%;

    @include xl {
      min-width: 100%;
      min-height: 45px;
    }

  }

  &__action {
    flex: 0 1 50%;
  }

  &__action-title {
    font-size: 40px;
    font-weight: 500;
    margin: 0 0 30px;

    @include lg {
      font-size: 32px;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__action-wrap {}

  &__action-item {
    min-height: 90px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background: $c-white;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 14px 17px;

    @include md {
      padding: 15px;
    }

    @include xs {
      display: block;
    }

    &:not(:last-of-type) {
      margin: 0 0 5px;

      @include xs {
        margin: 0 0 15px;
      }
    }
  }

  &__action-item-info {
    display: flex;
    align-items: center;
    margin: 0 20px 0 0;

    @include xs {
      margin: 0 0 20px;
    }
  }

  &__action-item-icon {
    width: 62px;
    height: 62px;
    flex-shrink: 0;
    margin: 0 15px 0 0;

    @include xl {
      width: 48px;
      height: 48px;
    }

    @include xs {
      margin: 0 5px 0 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__action-item-info-wrap {}

  &__action-item-name {
    font-size: 14px;
    color: $c-label;
    margin: 0 0 5px;

    @include xl {
      font-size: 12px;
    }
  }

  &__action-item-descr {
    font-size: 18px;

    @include xl {
      font-size: 14px;
    }
  }

  &__action-item-status {
    text-align: right;

    @include xs {
      text-align: left;
    }
  }

  &__action-item-status-cost {
    font-size: 14px;
  }

  &__action-item-status-text {
    font-size: 16px;
    color: $c-green;

    &--red {
      color: $c-accent;
    }
  }
}
