.user-header {
  position: relative;

  @media (min-width: 1200px) {
    &:hover {
      .user-header__popup {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        pointer-events: all;
      }
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    font-size: 20px;

    @include xl {
      font-size: 18px;
    }
  }

  &__btn-photo {
    width: 24px;
    height: 24px;
    flex-shrink: 0;

    @include lg {
      width: 32px;
      height: 32px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &__btn-name {
    margin: 0 0 0 15px;

    @include xs {
      display: none;
    }
  }

  &__popup {
    position: absolute;
    top: 100%;
    right: 0;
    z-index: 1;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translateY(30px);
    border-radius: 10px;
    background: $c-dark;
    padding: 20px;
    transition: opacity .3s, visibility .3s, transform .3s;

    &::before {
      content: '';
      width: 25px;
      height: 25px;
      position: absolute;
      top: -16px;
      right: 54px;
      background: url('../img/svg/triangle-user.svg') no-repeat center/contain;

      @include xs {
        right: 65px;
      }
    }

    &.active {
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      pointer-events: all;
    }

    @include xl {
      padding: 15px;
    }

    @include lg {
      top: calc(100% + 18px);
    }

    @include xs {
      position: fixed;
      top: 63px;
      left: 0;
      right: auto;
      width: 100vw;
      border-radius: 0;
    }
  }

  &__popup-info {
    display: flex;
    align-items: center;
    color: $c-white;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    padding: 0 0 20px;
    margin: 0 0 20px;

    @include xs {
      border-bottom: none;
      padding: 0;
      margin: 0 0 25px;
    }
  }

  &__popup-photo {
    width: 45px;
    height: 45px;
    flex-shrink: 0;
    margin: 0 15px 0 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &__popup-info-wrap {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }

  &__popup-name {
    font-size: 20px;
    color: $c-white;
    margin: 0 0 5px;

    @include xs {
      font-size: 36px;
    }
  }

  &__popup-mail {
    font-size: 13px;
    color: #7A7A7A;

    @include xs {
      font-size: 12px;
      color: $c-white;
    }
  }

  &__popup-menu {
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    margin: 0 0 20px;

    @include xs {
      margin: 0 0 30px;
      border-bottom: none;
    }
  }

  &__popup-menu-item {
    margin: 0 0 20px;

    @include xs {
      margin: 0;

      &:not(:last-of-type) {
        padding: 0 0 25px;
        margin: 0 0 25px;
        border-bottom: 1px solid $c-label;
      }
    }

    a {
      color: $c-white;
      transition: color .3s;

      @include xl {
        font-size: 14px;
      }

      @include xs {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 16px;

        &::after {
          content: '';
          border: 2px solid $c-white;
          width: 8px;
          height: 8px;
          flex-shrink: 0;
          margin: 0 10px 0 20px;
          border-bottom: none;
          border-left: none;
          transform: rotate(45deg);
        }
      }

      &:hover {
        color: $c-secondary;
      }
    }
  }

  &__popup-exit {
    font-weight: 500;
    font-size: 18px;
    color: $c-accent;

    @include xs {
      display: flex;
      align-items: center;
      justify-content: center;
      text-align: center;
      min-height: 60px;
      font-size: 16px;
      border: 1px solid #2C2C2C;
    }
  }
}
