.news-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 -60px;

  @include xl {
    margin: 0 0 -45px;
  }

  @include md {
    justify-content: space-between;
  }

  @include sm {
    margin: 0 0 -20px;
  }

  &__item {
    width: calc(33.33% - 15px * 2 / 3);
    margin: 0 15px 60px 0;

    @include xl {
      margin: 0 15px 45px 0;
    }

    @include md {
      width: calc(50% - 15px / 2);
      margin: 0 0 45px;
    }

    @include sm {
      width: 100%;
      margin: 0 0 25px;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}
