.course-features {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 0 -10px;

  @include xl {
    margin: 0 0 -5px;
  }

  @include md {
    margin: 0 0 -10px;
  }

  &__item {
    min-height: 66px;
    width: calc(50% - 10px / 2);
    display: flex;
    align-items: center;
    font-size: 16px;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 14px;
    margin: 0 0 10px;
    transition: border .3s;

    @include xl {
      min-height: 45px;
      width: calc(50% - 5px / 2);
      font-size: 12px;
      padding: 10px;
      margin: 0 0 5px;
    }

    @include md {
      width: 100%;
      margin: 0 0 10px;
    }

    @include xs {
      min-height: 55px;
      font-size: 14px;
      padding: 10px 10px 10px 18px;
    }

    &::before {
      content: '';
      width: 8px;
      height: 8px;
      background: $c-text;
      flex-shrink: 0;
      border-radius: 50%;
      margin: 0 18px 0 0;
      transition: background .3s;

      @include xl {
        width: 6px;
        height: 6px;
        margin: 0 10px 0 0;
      }
    }
  }
}
