.course-card {
  min-height: 342px;
  color: $c-text;
  background: $c-white;
  border-radius: 15px;
  padding: 30px;
  transition: box-shadow .3s;

  &:hover {
    box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
  }

  @include xl {
    min-height: 335px;
    padding: 30px 20px;
  }

  @include lg {
    min-height: auto;
  }

  @include sm {
    padding: 15px 15px 30px;
  }

  &__experience {
    display: inline-block;
    font-size: 16px;
    color: $c-accent;
    border: 1px solid $c-accent;
    border-radius: 30px;
    padding: 10px;
    margin: 0 0 15px;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      padding: 5px 10px;
    }
  }

  &__name {
    font-weight: 500;
    font-size: 36px;
    margin: 0 0 15px;

    @include xl {
      font-size: 32px;
    }

    @include md {
      font-size: 28px;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__descr {
    margin: 0 0 30px;
  }

  &__info {
    font-size: 18px;
    font-weight: 500;

    @include xs {
      font-size: 14px;
    }
  }

  &__info-text {
    &:not(:last-of-type) {
      margin: 0 0 5px;
    }
  }
}
