.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;

  display: flex;
  flex-direction: column;
  overflow-y: auto;

  background: rgba(49, 49, 48, 0.4);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;

  transition: opacity $modal-transition-settings,
    transform $modal-transition-settings,
    visibility $modal-transition-settings;

  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }

  &__wrapper {
    position: relative;

    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 24px;

    @include xs {
      padding: 60px 16px;
    }
  }

  &__overlay {
    position: absolute;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    cursor: pointer;
  }

  &__content {
    position: relative;
  }

  &__close-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 20px;
    right: 30px;
    width: 44px;
    height: 44px;
    border: none;
    background: $c-border;
    border-radius: 50%;
    cursor: pointer;

    &:hover {

      &::before,
      &::after {
        background: $c-accent;
      }
    }

    @include lg {
      width: 34px;
      height: 34px;
      top: 10px;
      right: 10px;
    }

    &::before,
    &::after {
      content: '';
      background: #252733;
      width: 14px;
      height: 2px;
      transition: .3s;

      @include lg {
        width: 10px;
      }
    }

    &::before {
      position: absolute;
      top: 21px;
      transform: rotate(45deg);

      @include lg {
        top: 16px;
      }
    }

    &::after {
      position: absolute;
      bottom: 21px;
      transform: rotate(-45deg);

      @include lg {
        bottom: 16px;
      }
    }
  }

  &--responsive {
    .modal__content {
      width: 100%;
    }
  }

  &--fit-content {
    .modal__content {
      width: auto;

      @include sm {
        width: 100%;
      }
    }
  }

  &--no-scale {
    transform: none;
  }

  &--preload {
    display: none;
  }

  &--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto;
  }
}
