.about-courses {
  padding-top: 90px;

  @include md {
    padding-top: 60px;
  }

  @include xs {
    padding-top: 45px;
  }

  &__head {
    max-width: 1448px;
    width: 100%;

    @include xl {
      max-width: 983px;
    }

    .landing-section__subtitle {
      max-width: 877px;
      width: 100%;
    }
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 -15px;

    @include lg {
      justify-content: space-between;
    }
  }

  &__item {
    min-height: 450px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: calc(25% - 15px * 3 / 4);
    border: 1px solid $c-border;
    border-radius: 15px;
    padding: 20px 45px;
    margin: 0 15px 15px 0;

    @include xl {
      padding: 20px;
    }

    @include lg {
      width: calc(50% - 15px / 2);
      margin: 0 0 15px;
    }

    @include sm {
      width: 100%;
    }

    @include xs {
      padding: 20px 15px;
    }

    &:nth-child(4n) {
      margin-right: 0;
    }
  }

  &__item-icon {
    width: 124px;
    height: 124px;
    margin: 0 0 50px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__item-name {
    font-weight: 500;
    font-size: 24px;
    margin: 0 0 15px;
  }

  &__item-descr {
    font-size: 18px;
    color: rgba(53, 53, 53, 0.8);
    margin: 0 0 20px;

    @include xs {
      font-size: 16px;
    }
  }

  &__item-btn {
    min-width: 100%;
    border: 1px solid $c-accent;
    margin: auto 0 0;

    @include xl {
      min-width: 100%;
      min-height: 60px;
    }

    @include xs {
      font-size: 14px;
    }
  }
}
