.messages {
  display: flex;
  height: calc(100vh - 317px);

  @include xl {
    height: calc(100vh - 312px);
  }

  @include md {
    height: auto;
  }

  &--admin {
    height: calc(100vh - 326px);

    @include xl {
      height: auto;
    }
  }

  &__chats {
    width: 410px;
    flex-shrink: 0;
    margin: 30px 28px 0 0;
    overflow-y: auto;

    @include xl {
      width: 320px;
    }

    @include md {
      width: 100%;
      overflow-y: inherit;
      padding: 0 0 60px;
      margin: 30px 0 0;
    }

    &--mob-hidden {
      @include md {
        display: none;
      }
    }
  }

  &__chats-item {
    min-height: 60px;
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    text-align: left;
    background: $c-white;
    border-radius: 15px;
    border: 1px solid $c-border;
    padding: 10px 60px 10px 10px;

    @include xl {
      padding: 6px 55px 6px 10px;
    }

    &.active {
      border-color: transparent;
      box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
    }

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }
  }

  &__chats-item-photo {
    width: 62px;
    height: 62px;
    margin: 0 10px 0 0;
    flex-shrink: 0;

    @include xl {
      width: 42px;
      height: 42px;
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  &__chats-item-info {
    white-space: nowrap;
    overflow: hidden;
  }

  &__chats-item-name {
    font-size: 20px;
    margin: 0 0 5px;

    @include xl {
      font-size: 14px;
    }
  }

  &__chats-item-msg {
    font-size: 16px;
    color: $c-label;

    @include xl {
      font-size: 12px;
    }
  }

  &__chats-item-time {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 16px;
    color: $c-label;

    @include xl {
      top: 8px;
    }
  }

  &__chats-item-num {
    position: absolute;
    top: 37px;
    right: 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 13px;
    color: $c-white;
    background: rgba($color: $c-accent, $alpha: .5);
    width: 18px;
    height: 18px;
    border-radius: 50%;

    @include xl {
      top: 32px;
    }
  }

  &__chat {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    border-left: 1px solid $c-border;
    padding: 15px 0 40px 30px;
    overflow-y: auto;

    &--mob-hidden {
      @include md {
        display: none;
      }
    }

    @include md {
      min-height: 500px;
      border: none;
      padding: 15px 0 60px;
    }
  }

  &__chat-wrap {
    margin: 0 0 60px;

    @include md {
      margin: 0 0 20px;
    }
  }

  &__chat-item {
    display: flex;
    max-width: 730px;
    width: 100%;

    &:not(:last-of-type) {
      margin-bottom: 30px;
    }

    @include md {
      max-width: none;
      width: calc(100% - 30px);
    }

    &--left {
      .messages__chat-item-text {
        background: $c-white;
        box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
        border-radius: 0px 20px 20px 20px;
      }
    }

    &--right {
      justify-content: flex-end;
      margin-left: auto;

      .messages__chat-item-text {
        color: $c-white;
        background: $c-accent;
        box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.24), 0px 2px 4px rgba(0, 0, 0, 0.16), 0px -1px 1px rgba(0, 0, 0, 0.08);
        border-radius: 24px 0px 24px 24px;
      }
    }
  }

  &__chat-item-photo {
    width: 32px;
    height: 32px;
    margin: 0 10px 0 0;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }

    @include xs {
      display: none;
    }
  }

  &__chat-item-text {
    padding: 12px 20px;

    @include xl {
      font-size: 14px;
    }
  }

  &__chat-item-time {
    align-self: center;
    flex-shrink: 0;
    font-size: 16px;
    color: rgba($color: $c-label, $alpha: .5);
    margin: 0 0 0 15px;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      display: none;
    }
  }

  &__chat-when {
    text-align: center;
    color: $c-label;
    margin: 0 0 30px;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      display: none;
    }
  }

  &__chat-enter {
    display: flex;
    align-items: center;
    margin: auto 0 0;
  }

  &__chat-enter-photo {
    width: 60px;
    height: 60px;
    margin: 0 15px 0 0;
    flex-shrink: 0;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }

    @include xs {
      display: none;
    }
  }

  &__chat-enter-wrap {
    box-sizing: content-box;
    width: 100%;
    border: 1px solid $c-border;
    border-radius: 20px;
    font-size: 18px;
    padding: 12px 20px;

    @include xs {
      display: none;
    }
  }

  &__chat-enter-textarea {
    width: 100%;
    overflow: hidden;
    border: none;

    &::placeholder {
      color: rgba($color: $c-text, $alpha: .5);
    }
  }

  &__chat-enter-textarea-mob {
    display: none;

    @include xs {
      display: block;
      width: 100%;
      height: 144px;
      border: 1px solid $c-border;
      border-radius: 20px;
      padding: 12px 20px;
      font-size: 18px;

      &::placeholder {
        color: rgba($color: $c-text, $alpha: .5);
      }
    }
  }
}
