.course-completed {
  padding: 220px 0 100px;

  @include xl {
    padding: 190px 0 80px;
  }

  @include md {
    padding: 155px 0 60px;
  }
  @include xs {
    padding: 105px 0 60px;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
  }

  &__level {
    border: 1px solid $c-border;
    font-size: 16px;
    border-radius: 36px;
    padding: 10px 20px;
    margin: 0 0 15px;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      padding: 5px 20px;
    }

    &--red {
      color: rgba($color: $c-accent, $alpha: .8);
    }
  }

  &__title {
    font-weight: 500;
    font-size: 36px;
    margin: 0 0 60px;

    @include md {
      font-size: 28px;
    }

    @include xs {
      font-size: 24px;
    }
  }

  &__stat {
    width: 100%;
    align-self: flex-start;
    margin: 0 0 60px;
  }

  &__experience {
    font-size: 16px;
    margin: 0 0 30px;

    @include xl {
      font-size: 14px;
      margin: 0 0 15px;
    }

    @include xs {
      text-align: center;
    }

    &--yellow {
      font-weight: 500;
      color: $c-secondary;
    }
  }

  &__stat-line {
    position: relative;
    height: 10px;
    border-radius: 5px;
    background: $c-text;
    margin: 0 0 30px;
  }

  &__stat-line-inner {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    background: $c-accent;
    height: 100%;
    border-radius: inherit;
  }

  &__stat-wrap {
    display: flex;
    flex-wrap: wrap;
    font-size: 18px;
    margin: 0 0 -15px;

    @include xl {
      font-size: 14px;
    }

    @include xs {
      display: block;
      font-size: 12px;
    }
  }

  &__stat-item {
    margin: 0 30px 15px 0;

    @include xs {
      margin: 0 0 15px;
    }
  }

  &__stat-item-val {
    color: $c-accent;
  }

  &__btns {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -7px -15px;

    @include xs {
      margin: 0 0 -15px;
    }
  }

  &__btns-item {
    font-size: 14px;
    margin: 0 7px 15px;

    @include xl {
      min-width: 250px;
      min-height: 60px;
      font-size: 14px;
    }

    @include xs {
      min-width: 100%;
      margin: 0 0 15px;
    }
  }
}
