.user-stat {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: $c-secondary;
  border-radius: 5px;
  padding: 15px 30px;

  @include lg {
    display: block;
    padding: 15px;
  }

  @include xs {
    padding: 15px 15px 10px;
  }

  &__info {
    display: flex;
    align-items: center;
    margin: 0 20px 0 0;

    @include lg {
      margin: 0 0 15px;
    }
  }

  &__info-icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 60px;
    height: 60px;
    flex-shrink: 0;
    background: $c-accent;
    border-radius: 15px;
    margin: 0 15px 0 0;

    @include md {
      width: 50px;
      height: 50px;
    }

    @include xs {
      border-radius: 5px;
      width: 33px;
      height: 35px;
      margin: 0 8px 0 0;
    }

    img {
      width: 40px;
      height: 30px;
      object-fit: cover;

      @include md {
        width: 30px;
        height: 25px;
      }

      @include xs {
        width: 18px;
        height: 16px;
      }
    }
  }

  &__info-wrap {}

  &__info-experience {
    font-weight: 700;
    font-size: 18px;

    @include xs {
      font-size: 14px;
    }
  }

  &__info-text {
    font-size: 12px;

    &--red {
      font-weight: 500;
      font-size: 14px;
      color: $c-accent;

      @include xs {
        font-size: 12px;
      }
    }
  }

  &__points {
    max-width: 738px;
    width: 100%;
    display: flex;
    align-items: center;
    font-weight: 500;
    font-size: 14px;
    color: $c-white;

    @include lg {
      max-width: none;
    }

    @include xs {
      font-size: 12px;
    }
  }

  &__points-num {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 45px;
    background: $c-accent;
    border-radius: 5px;
    padding: 5px 18px;

    @include xs {
      height: 30px;
      padding: 5px 9px;
    }
  }

  &__points-line {
    position: relative;
    flex-grow: 1;
    height: 10px;
    border-radius: 5px;
    background: $c-accent;
    margin: 0 50px;

    @include md {
      margin: 0 20px;
    }

    @include xs {
      height: 5px;
      font-weight: 400;
      margin: 0 10px;
    }
  }

  &__points-val {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    background: $c-accent;
    border-radius: 50%;

    @include xs {
      width: 22px;
      height: 22px;
    }
  }
}
