.landing-reviews {

  &__head {
    max-width: 875px;
    width: 100%;

    @include xl {
      max-width: 600px;
    }
  }

  &__wrap {}

  &__slider {
    @media (max-width: 1280px) {
      margin: 0 -25px;
      padding: 0 0 0 25px;
    }

    @include xs {
      margin: 0 -15px;
      padding: 0 0 0 15px;
    }

    &:first-of-type {
      margin-bottom: 15px;
    }
  }

  &__item {

    .review-video__cover {
      border: 1px solid $c-border;
    }
  }
}
