.home-courses {
  padding-top: 90px;

  @include md {
    padding-top: 60px;
  }

  @include xs {
    padding-top: 45px;
  }

  &__head {
    max-width: 1332px;
    width: 100%;

    @include xl {
      max-width: 924px;
    }

    .landing-section__subtitle {
      max-width: 889px;
      width: 100%;

      @include xl {
        max-width: 600px;
      }
    }
  }

  &__slider {
    @media (max-width: 1280px) {
      margin: 0 -25px;
      padding: 0 0 0 25px;
    }

    @include xs {
      margin: 0 -15px;
      padding: 0 0 0 15px;
    }
  }

  &__item {
    position: relative;
    z-index: 1;
    background: $c-substrate;
    border-radius: 15px;
    overflow: hidden;
    transition: color .3s;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: -1;
      opacity: 0;
      width: 100%;
      height: 100%;
      background: $c-accent url('../img/home/courses-el.png') no-repeat bottom/contain;
      transition: opacity .5s ease;
    }

    &:hover {
      color: $c-white;

      .course-features__item {
        border-color: $c-white;
      }

      .course-features__item::before {
        background: $c-white;
      }

      .home-courses__item-footer {
        border-color: $c-white;
      }

      .home-courses__item-btn {
        background: $c-secondary;
      }

      &::before {
        opacity: 1;
      }
    }
  }

  &__item-info {
    padding: 50px 20px 60px 35px;

    @include xl {
      padding: 20px 20px 45px;
    }

    @include xs {
      padding: 30px 17px 45px;
    }
  }

  &__item-name {
    font-weight: 500;
    font-size: 52px;
    margin: 0 0 15px;

    @include xl {
      font-size: 32px;
      margin: 0 0 10px;
    }

    @include xs {
      font-size: 30px;
      margin: 0 0 15px;
    }
  }

  &__item-descr {
    margin: 0 0 60px;

    @include xl {
      font-size: 14px;
      margin: 0 0 40px;
    }

    @include xs {
      font-size: 16px;
      margin: 0 0 20px;
    }
  }

  &__item-features {}

  &__item-footer {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid $c-border;
    padding: 30px 30px 30px 60px;
    transition: border .3s;

    @include xl {
      padding: 20px 30px 25px;
    }

    @include xs {
      border-top: none;
      padding: 0 17px 22px;
    }
  }

  &__item-price {
    font-weight: 500;
    font-size: 52px;
    margin: 0 20px 0 0;

    @include xl {
      font-size: 34px;
    }

    @include xs {
      font-size: 28px;
    }
  }

  &__item-btn {
    min-width: auto;
    min-height: 50px;
    color: $c-text;
    padding: 0 18px;

    @include xl {
      min-width: auto;
    }
  }
}
