.news-card {
  position: relative;

  &__img {
    height: 350px;
    background: $c-substrate;
    border-radius: 15px;
    margin: 0 0 15px;

    @include xl {
      height: 250px;
    }

    @include sm {
      height: 350px;
    }

    @include xs {
      height: 250px;
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
      object-position: top;
    }
  }

  &__descr {
    font-size: 24px;
    margin: 0 0 15px;

    @include xl {
      font-size: 16px;
    }

    @include sm {
      font-weight: 500;
      font-size: 18px;
      margin: 0 0 8px;
    }
  }

  &__link {
    color: $c-label;
    padding: 0 0 3px;
    border-bottom: 1px dashed $c-border;
    transition: color .3s, border .3s;

    @include xl {
      font-size: 14px;
    }

    @include sm {
      font-size: 16px;
    }

    &:hover {
      color: $c-accent;
      border-color: $c-accent;
    }
  }

  &__popup.admin-popup {
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
