.admin-select {
  position: relative;
  width: 370px;
  font-size: 18px;
  background: $c-white;
  border: 1px solid $c-border;
  border-radius: 5px;

  @include xl {
    width: 300px;
    font-size: 14px;
  }

  &::after {
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 18px;
    width: 24px;
    height: 24px;
    background: url('../img/svg/chevron-select.svg') no-repeat center/cover;
    pointer-events: none;
  }

  select {
    outline: none;
    appearance: none;
    background-color: transparent;
    border: none;
    margin: 0;
    width: 100%;
    padding: 20px 65px 20px 18px;
    color: $c-label;
    font-family: inherit;
    font-size: inherit;
    cursor: pointer;
    line-height: inherit;

    @include xl {
      padding: 15px 55px 15px 18px;
    }
  }
}
