.tags {
  display: flex;
  flex-wrap: wrap;

  &__item {
    font-size: 16px;
    color: rgba(0, 0, 0, 0.3);
    border: 1px solid rgba(0, 0, 0, 0.3);
    border-radius: 30px;
    padding: 10px 15px;
    margin-bottom: 5px;
    transition: color .3s, border .3s;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      padding: 6px 15px;
    }

    &:hover:not(.active) {
      color: $c-text;
      border-color: $c-border;
    }

    &.active {
      color: $c-text;
      border-color: $c-border;
    }

    &:not(:last-of-type) {
      margin-right: 15px;

      @include xs {
        margin-right: 5px;
      }
    }
  }
}
