.news {

  &__tags {
    margin: 0 0 25px;

    @include xs {
      margin: 0 0 10px;
    }
  }
  &__wrap {
    display: flex;

    @include sm {
      flex-wrap: wrap;
    }
  }

  &__item {
    flex: 0 0 540px;

    @include xl {
      flex: 0 0 400px;
    }

    @include md {
      flex: 0 1 50%;
    }

    @include sm {
      flex: 1 1 100%;
    }

    &--big {
      flex: 1 1 auto;
      margin: 0 15px 0 0;

      @include xl {
        flex: 1 1 auto;
      }

      @include sm {
        margin: 0 0 20px;
      }

      .news-card__img {
        height: 515px;

        @include md {
          height: 250px;
        }

        @include sm {
          height: 350px;
        }

        @include xs {
          height: 250px;
        }
      }
    }
  }
}
