.admin-courses {
  margin: 60px 0 0;

  @include xl {
    margin: 30px 0 0;
  }

  &__table {
    width: 100%;

    tr {
      transition: box-shadow .3s, border .3s;

      &:hover:not(:first-of-type) {
        box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
        border-radius: 0px 15px 15px 0px;

        td:first-of-type {
          border-color: $c-accent;
        }
      }
    }

    th {
      font-size: 16px;
      color: $c-label;
      padding: 0 30px 15px;
    }

    td {
      min-height: 30px;
      font-size: 18px;
      white-space: nowrap;
      text-align: center;
      padding: 30px;

      @media (max-width: 1600px) {
        font-size: 14px;
      }

      &:first-of-type {
        border-left: 3px solid transparent;
        color: $c-label;
        transition: border .3s;

        @media (max-width: 1600px) {
          font-size: 18px;
        }
      }

      p {
        width: inherit;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }

  &__table-name {
    max-width: 320px;

    @media (max-width: 1600px) {
      max-width: 250px;
    }
  }

  &__table-descr {
    max-width: 580px;

    @media (max-width: 1600px) {
      max-width: 460px;
    }
  }

  &__table-experience {
    display: inline-block;
    font-size: 16px;
    color: $c-accent;
    border: 1px solid $c-accent;
    border-radius: 30px;
    padding: 10px;

    @media (max-width: 1600px) {
      font-size: 12px;
    }
  }

  &__more {}
}
