.admin-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid $c-border;
  padding: 28px;

  @include xl {
    padding-left: 15px;
  }

  &__menu {
    margin: 0 20px 0 0;
  }

  &__menu-list {
    display: flex;
    margin: 0 -30px;
  }

  &__menu-item {
    padding: 0 30px;

    a {
      font-size: 18px;
      color: $c-text;
      transition: color .3s;

      &:hover {
        color: $c-accent;
      }
    }
  }

  &__profile {
    display: flex;
    align-items: center;
    font-size: 20px;
  }

  &__profile-photo {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    margin: 0 15px 0 0;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
