.admin {
  display: flex;
  align-items: flex-start;

  &__main {
    flex: 1 1 auto;
  }

  &__main-head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid $c-border;
    padding: 28px;

    @include xl {
      padding-left: 15px;
    }
  }

  &__main-head-left {
    display: flex;
    align-items: center;
    margin: 0 20px 0 0;
  }

  &__main-title {
    font-weight: 500;
    font-size: 36px;

    @include xl {
      font-size: 32px;
    }
  }

  &__main-search {
    position: relative;
    margin: 0 0 0 30px;
  }

  &__main-search-input {
    width: 470px;
    padding-left: 50px;
  }

  &__main-search-icon {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 20px;
    width: 17px;
    height: 17px;
  }

  &__main-head-right {
    display: flex;
    align-items: center;
  }

  &__main-head-filter {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 38px;
    height: 38px;
    flex-shrink: 0;
    margin: 0 30px 0 0;

    img {
      width: 27px;
      height: 19px;
    }
  }

  &__main-content {
    padding: 30px 30px 90px;

    @include xl {
      padding-left: 15px;
    }

    &--messages {
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  &__main-section {
    &:not(:last-of-type) {
      margin: 0 0 30px;
    }

    .review-text .review-text__content {
      font-size: 18px;

      @include xl {
        font-size: 14px;
      }
    }

    .more .btn {
      min-height: 80px;

      @include xl {
        min-height: 75px;
      }
    }
  }

  &__main-content-title {
    font-weight: 400;
    font-size: 24px;
    margin: 0 0 30px;

    &--bold {
      font-weight: 500;
    }
  }

  &__main-section-item {
    margin: 0 0 30px;
  }

  &__main-section-item-name {
    color: $c-label;
    font-size: 14px;
    margin: 0 0 15px;

    @include xl {
      font-size: 12px;
    }
  }

  &__main-section-item-wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 -10px;
  }

  &__main-section-item-wrap-el {
    margin: 0 15px 10px 0;
  }

  &__main-section-item-bulit {
    min-width: 270px;
    height: 60px;
    font-size: 16px;
    color: $c-label;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 10px 18px;

    @include xl {
      min-width: 200px;
      height: 45px;
      font-size: 14px;
      padding: 5px 18px;
    }
  }

  &__main-textarea {
    width: 100%;
    height: 290px;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 20px 18px;
    transition: color .3s, border .3s;


    &::placeholder {
      color: $c-label;
    }

    &:hover {
      border-color: $c-accent;
      color: $c-accent;
    }
  }


}
