.about-hero {
  position: relative;
  z-index: 1;
  background: $c-accent;
  min-height: 750px;
  padding: 220px 0 100px;

  @include xl {
    min-height: 697px;
    padding: 190px 0 100px;
  }

  @include lg {
    min-height: 550px;
    padding: 180px 0 20px;
  }

  @include xs {
    min-height: auto;
  }

  &__iroglyphs {
    position: absolute;
    z-index: -1;

    @include lg {
      display: none;
    }
  }

  &__iroglyphs--1 {
    width: 489px;
    height: 470px;
    object-fit: cover;
    left: 60px;
    bottom: -67px;

    @media (max-width: 1870px) {
      width: 436px;
      height: 410px;
      bottom: -42px;
    }
  }

  &__iroglyphs--2 {
    width: 550px;
    height: 530px;
    object-fit: cover;
    right: 60px;
    bottom: -100px;

    @media (max-width: 1870px) {
      width: 428px;
      height: 420px;
      bottom: -69px;
    }
  }

  &__wrap {
    max-width: 720px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $c-white;
    text-align: center;
    margin: 0 auto;
  }

  &__title {
    font-size: 76px;
    font-weight: 500;
    line-height: 1.1;
    margin: 0 0 15px;

    @include xl {
      font-size: 50px;
      margin: 0 0 15px;
    }

    @include xs {
      font-size: 34px;
    }
  }

  &__subtitle {
    max-width: 320px;
    width: 100%;

    @include xl {
      font-size: 16px;
    }

    @include xs {
      margin: 0 0 120px;
    }
  }

  &__btn-down {
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    width: 44px;
    height: 44px;
    border: 1px solid $c-white;

    @include xs {
      display: inline-flex;
    }

    img {
      width: 24px;
      height: 24px;
    }
  }
}
