.page-main {
  padding: 130px 0 0;

  @include lg {
    padding: 140px 0 0;
  }

  @include xs {
    padding: 90px 0 0;
  }

  &__head {
    max-width: 880px;
    width: 100%;
    margin: 0 0 60px;

    @include xl {
      margin: 0 0 45px;
    }
  }

  &__title {
    margin: 0 0 15px
  }

  &__subtitle {
    @include xs {
      padding: 0 40px 0 0;
    }
  }
}

.page-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 -15px;

  @include lg {
    justify-content: space-between;
  }

  &__item {
    width: calc(33.33% - 15px * 2 / 3);
    margin: 0 15px 15px 0;

    @include lg {
      width: calc(50% - 15px / 2);
      margin: 0 0 15px;
    }

    @include sm {
      width: 100%;
    }

    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}

.title {
  @include title-h1;

  @include xl {
    font-size: 40px;
  }

  @include lg {
    font-size: 32px;
  }

  @include md {
    font-size: 28px;
  }

  @include xs {
    font-size: 24px;
  }

  &--sm {
    @include title-h2;

    @include xl {
      font-size: 32px;
    }

    @include lg {
      font-size: 28px;
    }

    @include md {
      font-size: 24px;
    }

    @include xs {
      font-size: 20px;
    }
  }
}

.section {
  padding-bottom: 60px;

  @include xs {
    padding-bottom: 45px;
  }

  &--grey {
    background: $c-substrate;
  }

  &:last-of-type {
    padding-bottom: 120px;

    @include xl {
      padding-bottom: 90px;
    }

    @include xs {
      padding-bottom: 60px;
    }

  }

  &__title {
    margin: 0 0 60px;

    @include xl {
      margin: 0 0 45px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }
}

.about-wrap {
  display: flex;
  flex-direction: column;

  .about-features {
    @include xs {
      order: 1;
    }
  }
}

@keyframes faded {
  from {
    opacity: 0.1;
  }

  to {
    opacity: 1;
  }
}
