.course-desktop {
  padding-top: 60px;

  @include xs {
    padding-top: 45px;
  }

  &__title {
    margin: 0 0 30px;

    @include xl {
      margin: 0 0 45px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 45px;

    @include xl {
      margin: 0 0 40px;
    }

    @include md {
      margin: 0 0 30px;
    }

    @include sm {
      justify-content: space-between;
    }

    @include xs {
      margin: 0 0 10px;
    }
  }

  &__item {
    display: flex;
    align-items: center;
    min-height: 185px;
    width: calc(25% - 20px * 3 / 4);
    font-size: 14px;
    color: $c-text;
    background: $c-substrate;
    border-radius: 5px;
    padding: 40px;
    margin: 0 20px 15px 0;
    transition: box-shadow .3s;

    @include xl {
      min-height: 160px;
      padding: 30px 20px;
      margin: 0 20px 20px 0;
    }

    @include lg {
      min-height: 100px;
      width: calc(33.33% - 20px * 2 / 3);
      font-size: 12px;
      padding: 20px 15px;
      margin: 0 20px 20px 0;
    }

    @include sm {
      width: calc(50% - 10px / 2);
      margin: 0 0 20px;
    }

    @include xs {
      padding: 15px 10px;
    }

    &:nth-child(4n) {
      margin-right: 0;

      @include lg {
        margin-right: 20px;
      }

      @include sm {
        margin-right: 0;
      }
    }

    &:nth-child(3n) {
      @include lg {
        margin-right: 0;
      }
    }

    &:hover {
      box-shadow: 0px 9.03012px 27.0904px rgba(197, 176, 176, 0.1), 0px 3.38629px 5.64383px rgba(197, 176, 176, 0.32);

      .course-desktop__item-icon {
        background: $c-accent;

        svg {
          fill: $c-white;
        }
      }
    }
  }

  &__item-icon {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    width: 70px;
    height: 70px;
    border-radius: 50%;
    background: #E9E9E9;
    margin: 0 30px 0 0;
    transition: background .3s;

    @include xl {
      width: 60px;
      height: 60px;
    }

    @include lg {
      width: 40px;
      height: 40px;
      margin: 0 15px 0 0;
    }

    @include xs {
      width: 30px;
      height: 30px;
      margin: 0 10px 0 0;
    }

    svg {
      width: 24px;
      height: 24px;
      fill: #000;
      transition: fill .3s;

      @include lg {
        width: 18px;
        height: 18px;
      }

      @include xs {
        width: 14px;
        height: 14px;
      }
    }
  }

  &__item-info {}

  &__item-name {
    font-weight: 500;
    font-size: 24px;
    margin: 0 0 5px;

    @include xl {
      font-size: 20px;
    }

    @include lg {
      font-size: 18px;
    }

    @include sm {
      font-size: 16px;
    }

    @include xs {
      font-size: 14px;
    }
  }

  &__item-descr {}

  &__stat {}
}
