.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  color: $c-text;
  background: $c-white;
  border-bottom: 1px solid $c-border;
  padding: 38px 0 30px;

  @include xs {
    padding: 15px 0;
  }

  &--home {
    background: transparent;
    border-bottom: none;

    .header__logo {
      font-size: 44px;

      @include xl {
        font-size: 30px;
      }

      @include xs {
        font-size: 20px;
      }
    }

    .header__logo-svg {
      display: block;
      width: 44px;
      height: 36px;

      @include xl {
        width: 30px;
        height: 25px;
      }
    }

    .header__logo-img {
      display: none;
    }

    .header__nav-link {
      &:hover:not(.active) {
        color: $c-dark;
      }
    }
  }

  &--red {
    background: $c-accent;
    color: $c-white;

    .header__logo {
      color: $c-white;
    }

    .header__logo-svg {
      display: block;
      fill: $c-white;
    }

    .header__logo-img {
      display: none;
    }

    .header__nav-link {
      &:hover:not(.active) {
        color: $c-secondary;
      }
    }

    .header__nav-link.active {
      color: $c-secondary;
    }

    .user-header__btn-name {
      color: $c-white;
    }

    .header__btns-item {
      &:hover {
        color: $c-text;
        background: $c-secondary;
      }
    }

    .header__burger {
      background: $c-white;

      span,
      &::before,
      &:after {
        background: $c-text;
      }
    }
  }

  &--tasks {
    border-color: $c-text;
    background: transparent;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include lg {
      align-items: center;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    flex-shrink: 0;
    font-weight: 700;
    color: $c-text;
    font-size: 29px;
    font-family: $font-family-alegreya;
    margin: 0 20px 0 0;

    @include xl {
      font-size: 30px;
    }

    @include xs {
      font-size: 20px;
    }

    span {
      flex-shrink: 0;

      @include xs {
        margin: 5px 0 0;
      }
    }
  }

  &__logo-icon {
    width: 28px;
    height: 23px;
    margin: 0 10px 0 0;

    @include lg {
      width: 32px;
      height: 32px;
    }
  }

  &__logo-img {
    @include lg {
      display: none;
    }
  }

  &__logo-svg {
    display: none;
    fill: $c-text;

    @include xl {
      width: 35px;
      height: 35px;
    }

    @include lg {
      width: 32px;
      height: 32px;
      display: block;
    }
  }

  &__info {
    margin: 10px 20px 0 -185px;

    @include lg {
      position: fixed;
      top: 0;
      left: 0;
      transform: translateX(-100%);
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      width: 100vw;
      height: 100%;
      color: $c-text;
      background: $c-white;
      padding: 30px 30px 40px;
      z-index: 5;
      transition: 0.3s;
      overflow: auto;
      margin: 0;

      &.active {
        transform: translateX(0);
      }

      &::before {
        content: '';
        display: block;
        font-size: 42px;
        padding: 0 70px 36px 0;
      }
    }

    @include xs {
      padding: 15px 14px 32px;

      &::before {
        font-size: 36px;
      }
    }
  }

  &__info-close {
    display: none;

    @include lg {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      position: absolute;
      top: 15px;
      right: 15px;
      z-index: 1;
      width: 48px;
      height: 48px;
      border-radius: 50%;
      background: $c-substrate;

      &::before,
      &::after {
        content: '';
        background: $c-dark;
        width: 22px;
        height: 2px;
        position: absolute;
        border-radius: 0;
        transition: 0.3s;
      }

      &::before {
        top: 23px;
        transform: rotate(45deg);
      }

      &::after {
        bottom: 23px;
        transform: rotate(-45deg);
      }
    }

    @include xs {
      width: 45px;
      height: 45px;

      &::before {
        width: 13px;
        top: 21px;
        height: 2px;
      }

      &::after {
        width: 13px;
        height: 2px;
        bottom: 22px;
      }
    }
  }

  &__menu {
    @include lg {
      max-width: 575px;
      width: 100%;
    }
  }

  &__menu-list {
    display: flex;

    @include lg {
      display: block;
      margin: 0 0 38px;
    }
  }

  &__nav-item {
    padding: 0 30px;

    @include xl {
      padding: 0 20px;
    }

    @include lg {
      padding: 0;

      &:not(:last-of-type) {
        padding: 0 0 30px;
        margin: 0 0 30px;
        border-bottom: 1px solid #CCCCCC;
      }
    }

    &:first-of-type {
      .header__nav-link.active {
        border-color: $c-accent;
      }
    }
  }

  &__nav-link {
    color: inherit;
    padding: 0 0 12px;
    border-bottom: 1px solid transparent;
    transition: color .3s, border .3s;

    &:hover:not(.active) {
      font-weight: 500;
      color: $c-accent;
      border-color: $c-accent;
    }

    &.active {
      color: $c-accent;
    }

    @include xl {
      padding: 0 0 8px;
    }

    @include lg {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 20px;
      border-bottom: none;
      padding: 0;

      &::after {
        content: '';
        border: 2px solid $c-text;
        width: 8px;
        height: 8px;
        flex-shrink: 0;
        margin: 0 10px 0 20px;
        border-bottom: none;
        border-left: none;
        transform: rotate(45deg);
      }
    }

    @include xs {
      font-size: 16px;
    }
  }

  &__btns {
    display: flex;

    &.hidden {
      display: none;
    }

    @include lg {
      display: none;
    }

    &--mob {
      display: none;

      @include lg {
        display: flex;
        flex-direction: column;
        max-width: 575px;
        width: 100%;
        margin: auto 0 0;
      }
    }
  }

  &__btns-item {
    color: inherit;
    font-weight: 400;
    min-width: auto;
    padding: 0 33px;

    @include xl {
      min-width: auto;
      padding: 0 16px;
    }

    @include lg {
      min-height: 60px;
    }

    &:hover {
      background: $c-accent;
      color: $c-white;
      border-color: transparent;
    }
  }

  &__btns-enter {
    padding: 0 70px;
    border: 1px solid rgba(247, 247, 247, 0.5);

    &:hover {
      background: $c-accent;
      color: $c-white;
      border-color: transparent;
    }

    @include xl {
      padding: 0 45px;
    }

    @include lg {
      background: $c-accent;
      color: $c-white;
      border-color: transparent;
    }
  }

  &__user {
    margin: 9px 0 -18px;
    padding: 0 0 18px;

    &.hidden {
      display: none;

      @include xs {
        display: block;
      }
    }

    @include lg {
      margin: 0 0 0 auto;
      padding: 0;
    }
  }

  &__burger {
    display: none;

    @include lg {
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      flex-shrink: 0;
      width: 48px;
      height: 48px;
      background: $c-text;
      border-radius: 5px;
      position: relative;
      margin: 0 0 0 15px;

      span {
        background: $c-white;
        width: 22px;
        height: 2px;
        margin: 6px 0;
        border-radius: 2px;
        pointer-events: none;
        transition: 0.3s;
      }

      &::before,
      &::after {
        content: '';
        background: $c-white;
        width: 22px;
        height: 2px;
        border-radius: 5px;
        transition: 0.3s;
      }
    }

    @include xs {
      width: 32px;
      height: 32px;

      span {
        width: 14px;
        height: 1px;
        margin: 5px 0;
      }

      &::before,
      &::after {
        width: 14px;
        height: 1px;
      }
    }
  }
}
