.study {

  &__head {
    font-size: 18px;
    letter-spacing: 0.07em;
    color: rgba($color: $c-label, $alpha: .8);
    text-transform: uppercase;
    margin: 0 0 15px;

    @include xl {
      font-size: 12px;
    }

    @include lg {
      display: none;
    }
  }

  &__row {
    display: flex;
    justify-content: space-between;
    max-width: 1480px;
    width: calc(100% - 200px);

    @include xl {
      padding-left: 15px;
    }

    @include lg {
      width: 100%;
    }
  }

  &__cell {
    width: 25%;
    padding: 0 30px;

    @include xl {
      width: 24%;
      padding: 0 15px;

      &:not(:first-of-type) {
        text-align: center;
      }

      &:first-of-type {
        width: 28%;
      }
    }

    @include lg {
      padding: 0;
      width: 100%;

      &:not(:first-of-type) {
        text-align: left;
      }

      &:first-of-type {
        width: 100%;
      }
    }
  }

  &__item {
    display: flex;

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }

    @media (min-width: 1200px) {
      &:hover {
        .study__item-row {
          background: $c-white;
          border-radius: 0px 5px 5px 0px;
          border-color: $c-accent;
          box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
        }

        .study__item-btn {
          color: $c-white;
          background: $c-accent;
          border-color: transparent;

          &::after {
            opacity: 1;
          }
        }
      }
    }



    @include lg {
      background: $c-substrate;
      border-radius: 5px;
      padding: 15px;
    }

    @include sm {
      display: block;
    }
  }

  &__item-row {
    min-height: 95px;
    align-items: center;
    background: $c-substrate;
    border-radius: 5px;
    border-left: 3px solid transparent;
    padding: 15px 0;
    transition: border .3s, box-shadow .3s, background .3s;

    @include xl {
      min-height: 90px;
    }


    @include lg {
      min-height: auto;
      display: block;
      border-radius: 0;
      border: none;
      padding: 0;
      margin: 0 0 -15px;
    }

    @include sm {
      margin: 0;
    }
  }

  &__item-cell {
    font-size: 18px;
    overflow: hidden;

    &:first-of-type {
      font-weight: 500;
      font-size: 22px;

      @include xl {
        font-size: 18px;
      }

    }

    &:not(:first-of-type) {
      @include lg {
        font-size: 14px;
        color: rgba($color: $c-dark, $alpha: .8);

        &::before {
          content: attr(data-label);
          color: rgba($color: $c-label, $alpha: .8);
          font-size: 12px;
          margin: 0 5px 0 0;
        }
      }

      @include xs {
        font-size: 12px;
      }
    }

    @include lg {
      margin: 0 0 15px;


    }
  }

  &__item-btn {
    position: relative;
    min-width: 170px;
    max-width: 170px;
    min-height: 100%;
    font-size: 14px;
    margin: 0 0 0 30px;

    @include xl {
      min-width: 170px;
      max-width: 170px;
    }

    @include sm {
      min-width: 100%;
      min-height: 54px;
      margin: 0;
      justify-content: space-between;
    }

    &::after {
      content: '';
      position: absolute;
      top: 10px;
      right: 10px;
      width: 11px;
      height: 11px;
      opacity: 0;
      background: url('../img/svg/arrow-tr.svg') no-repeat center/cover;
      transition: opacity .3s;

      @include lg {
        opacity: 1;
        background: url('../img/svg/arrow-tr-black.svg') no-repeat center/cover;
      }

      @include sm {
        position: static;
        top: auto;
        right: auto;
        margin: 0 0 0 20px;
        flex-shrink: 0;
      }
    }
  }
}
