.cabinet {
  padding-top: 135px;

  @include lg {
    padding-top: 150px;
  }

  @include xs {
    padding-top: 95px;
  }

  &--tasks {
    background: $c-secondary;
    padding-bottom: 280px;

    @include xl {
      padding-bottom: 228px;
    }

    .user-nav {
      border-color: $c-text;
    }

    .cabinet__head {
      margin: 0;
    }
  }

  .more {
    display: none;

    @include xs {
      display: block;
    }
  }

  &__head {
    max-width: 750px;
    width: 100%;
    padding: 30px 0 0;
    margin: 0 0 60px;

    @include xl {
      margin: 0 0 45px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__head-back {
    display: block;
    color: rgba($color: $c-text, $alpha: .6);
    margin: 0 0 15px;
    transition: color .3s;

    &:hover {
      color: $c-accent;
    }

    @include xl {
      font-size: 14px;
    }

    @include xs {
      font-size: 12px;
      color: $c-text;
      margin: 0 0 30px;
    }
  }

  &__title {}

  &__subtitle {
    margin: 15px 0 0;

    @include xs {
      padding: 0 40px 0 0;
    }
  }
}
