.admin-nav {
  min-height: 1080px;
  width: 180px;
  flex-shrink: 0;
  background: $c-dark;
  margin: 0 0 80px;

  @include xl {
    width: 100px;
    min-height: 1024px;
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 87px;
    padding: 20px;

    svg {
      width: 30px;
      height: 30px;
      fill: $c-white;
    }
  }

  &__wrap {}

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 100px;
    font-weight: 700;
    font-size: 16px;
    color: $c-white;
    padding: 20px 10px;
    transition: background .3s;

    &.active,
    &:hover {
      color: $c-dark;
      background: $c-secondary;

      svg {
        fill: $c-dark;

        &.admin-nav__item-svg {
          fill: transparent;
          stroke: $c-dark;
        }
      }
    }

    @include xl {
      font-size: 14px;
    }

    svg {
      fill: $c-white;
      width: 24px;
      height: 24px;
      margin: 0 0 15px;

      @include xl {
        width: 16px;
        height: 16px;
      }

      &.admin-nav__item-svg {
        fill: transparent;
        stroke: $c-white;
      }
    }
  }

  &__item-name {}
}
