.landing-section {
  padding: 0 0 90px;

  @include md {
    padding: 0 0 60px;
  }

  @include xs {
    padding: 0 0 45px;
  }

  &__head {
    margin: 0 0 60px;

    @include xl {
      margin: 0 0 45px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__tag {
    display: inline-block;
    font-size: 16px;
    border: 1px solid $c-border;
    border-radius: 30px;
    padding: 7px 20px;
    margin: 0 0 15px;

    @include xl {
      font-size: 12px;
    }

    @include xs {
      padding: 5px 20px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 60px;
    margin: 0 0 15px;

    @include xl {
      font-size: 40px;
      margin: 0 0 10px;
    }

    @include md {
      font-size: 32px;
    }

    @include xs {
      font-size: 26px;
      margin: 0 0 15px;
    }
  }
}
