.not-found {
  min-height: 100vh;
  background: $c-accent;
  padding: 222px 0 100px;

  @include xl {
    padding: 160px 0 100px;
  }

  @include xs {
    padding: 122px 0 100px;
  }

  &__wrap {
    display: flex;
    flex-direction: column;
    align-items: center;
    color: $c-white;
    text-align: center;
  }

  &__num {
    font-weight: 100;
    font-size: 72px;
    margin: 0 0 5px;

    @include xl {
      font-size: 64px;
    }
  }

  &__chinese {
    font-weight: 100;
    font-size: 144px;
    margin: 0 0 60px;

    @include xl {
      font-size: 120px;
    }

    @include md {
      font-size: 80px;
    }

    @include xs {
      font-size: 60px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 40px;
    margin: 0 0 15px;

    @include xl {
      font-size: 30px;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__subtitle {
    max-width: 392px;
    width: 100%;
    font-size: 22px;
    margin: 0 0 30px;

    @include xl {
      max-width: 303px;
      font-size: 18px;
    }

    @include xs {
      font-size: 14px;
    }
  }

  &__btn {
    min-height: 75px;
    border: 1px solid $c-white;
    color: $c-white;

    @include xl {
      min-width: 250px;
      min-height: 50px;
    }

    @include xs {
      min-height: 60px;
      min-width: 100%;
    }

    &:hover {
      border-color: $c-secondary;
    }
  }
}
