.label {
  display: block;

  &__name {
    font-size: 16px;
    color: $c-label;
    margin: 0 0 15px;

    @include xs {
      font-size: 12px;
    }
  }


  &__birth-date {
    display: flex;
    max-width: 400px;
    width: 100%;

    input {
      text-align: center;

      &:nth-child(2) {
        margin: 0 15px;
      }

      &:first-of-type,
      &:last-of-type {
        width: 90px;

        @include xs {
          width: 60px;
        }
      }
    }
  }
}
