.home-feedback {
  background: $c-dark;
  padding: 40px 0;

  @include xl {
    padding: 90px 0;
  }

  @include lg {
    padding: 40px 0;
  }

  @include xs {
    padding: 40px 0 30px;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include md {
      display: block;
    }
  }

  &__text {
    color: $c-white;
    margin: 0 120px 0 0;

    @include lg {
      margin: 0 50px 0 0;
    }

    @include md {
      margin: 0 0 50px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 72px;
    margin: 0 0 15px;

    @include xl {
      font-size: 40px;
      margin: 0 0 10px;
    }

    @include xs {
      font-size: 39px;
      margin: 0 0 15px;
    }
  }

  &__subtitle {
    font-size: 22px;

    @include xl {
      font-size: 18px;
    }

    @include xs {
      font-size: 16px;
    }
  }

  &__form {
    flex: 0 0 605px;

    @include xl {
      flex: 0 0 420px;
    }

    @include md {
      max-width: 600px;
      width: 100%;
    }
  }

  &__form-wrap {
    display: flex;
    margin: 0 0 20px;

    @include xs {
      display: block;
      margin: 0 0 16px;
    }
  }

  &__input {
    width: 50%;
    height: 70px;
    font-size: 18px;
    color: $c-white;
    border: none;
    background: transparent;
    padding: 0 20px;
    transition: border .3s;

    &:disabled {
      color: #B8B8B8;
    }



    &:first-of-type {
      border: 1px solid $c-white;
      border-radius: 5px 0 0 5px;

      &:hover {
        border-right-color: $c-white;
      }

      @include xs {
        border: 1px solid #FFFFFF;
        border-radius: 5px;
        margin: 0 0 15px;
      }
    }

    &:last-of-type {
      border: 1px solid $c-white;
      border-left: none;
      border-radius: 0 5px 5px 0;

      @include xs {
        border: 1px solid #FFFFFF;
        border-radius: 5px;
      }
    }

    &:hover {
      border-color: $c-accent;
      color: $c-accent;
    }

    @include xl {
      height: 50px;
      font-size: 14px;
      padding: 0 15px;
    }

    @include xs {
      width: 100%;
      height: 70px;
      font-size: 18px;
      border: 1px solid #FFFFFF;
      border-radius: 5px;
    }

  }

  &__btn {
    min-width: 100%;
    min-height: 90px;

    @include xl {
      min-width: 100%;
      min-height: 60px;
    }
  }
}
