.input {
  width: 100%;
  height: 60px;
  font-size: 20px;
  color: $c-text;
  border: 1px solid $c-border;
  border-radius: 5px;
  padding: 0 14px;
  transition: color .3s, border .3s;

  &::placeholder {
    color: $c-label;
  }

  &--admin {
    font-size: 18px;
    padding: 0 18px;

    @include xl {
      font-size: 14px;
      height: 45px;
    }
  }

  @include md {
    font-size: 16px;
  }

  &:hover {
    border-color: $c-accent;
    color: $c-accent;
  }
}
