.user-nav {
    position: relative;
    border-bottom: 1px solid $c-border;

  &__content {
    margin: 0 90px 0 0;

    .swiper-slide {
      width: auto;
    }
  }

  &__item {
    display: inline-block;
    color: $c-text;
    border-bottom: 2px solid transparent;
    padding: 0 0 30px;
    transition: color .3s;

    &:hover:not(.active) {
      color: $c-accent;
    }

    &.active {
      font-weight: 500;
      color: $c-accent;
      border-color: $c-accent;

      @include xs {
        border-color: transparent;
      }
    }
  }

  &__btn {
    position: absolute;
    top: 0;
    right: 70px;
    width: 16px;
    height: 16px;

    &.swiper-button-disabled {
      display: none;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__btn-prev {
    position: absolute;
    right: 80px;

    img {
      transform: rotate(-180deg);
    }
  }

  &__chat {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 0 17px;
    margin: -17px 0 0;
  }

  &__chat-back {
    flex-shrink: 0;
    margin: 0 20px 0 0;
    font-size: 14px;
    color: $c-text;
  }

  &__chat-info {
    display: flex;
    align-items: center;
  }

  &__chat-photo {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    margin: 0 10px 0 0;

    img {
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }

  &__chat-name {
    font-size: 14px;
  }

  &__chat-msg {
    font-size: 12px;
    color: $c-label;
  }
}
