.btn {
  min-width: 300px;
  min-height: 60px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-weight: 500;
  font-size: 20px;
  padding: 0 15px;
  border-radius: 5px;
  transition: color .3s, background .3s, border .3s;

  @include xl {
    min-width: 200px;
    min-height: 40px;
    font-size: 16px;
  }

  &--red {
    background: $c-accent;
    color: $c-white;

    &:hover {
      background: darken($color: $c-accent, $amount: 7%);
    }

    .btn__icon {
      fill: $c-white
    }
  }

  &--white {
    background: $c-white;
    color: $c-accent;

    &:hover {
      background: $c-accent;
      color: $c-white;

      .btn__icon {
        fill: $c-white;
      }
    }

    .btn__icon {
      fill: $c-accent;
    }
  }

  &--orange {
    background: $c-secondary;
    color: $c-white;

    &:hover {
      background: #EFBF61;
    }

  }

  &--transparent {
    color: $c-text;
    border: 1px solid $c-border;

    &:hover {
      background: $c-accent;
      color: $c-white;
      border-color: transparent;

      svg {
        fill: $c-white;
      }
    }

    svg {
      flex-shrink: 0;
      margin: 0 10px 0 0;
      width: 24px;
      height: 24px;
      fill: $c-text;
      transition: fill .3s, stroke .3s;
    }
  }

  &--transparent-admin {
    min-width: 220px;
    font-weight: 400;
    font-size: 18px;
    margin: 15px 0 0;
    color: $c-label;

    @include xl {
      min-width: 180px;
      min-height: 45px;
      font-size: 14px;
      padding: 0 13px;
    }

    &:hover {
      svg {
        fill: transparent;

        path {
          &:first-of-type {
            stroke: $c-white;
          }

          &:last-of-type {
            fill: $c-white;
          }
        }
      }
    }

    svg {
      fill: transparent;
      margin: 0 0 0 15px;

      path {
        &:first-of-type {
          stroke: $c-label;
        }

        &:last-of-type {
          fill: $c-label;
        }
      }
    }
  }

  &--grey {
    background: $c-substrate;
    color: $c-text;

    &:hover {
      background: $c-accent;
      color: $c-substrate;
    }
  }

  &--admin {
    min-width: 200px;
    background: $c-accent;
    color: $c-white;
    padding: 0 20px;

    @include xl {
      min-height: 45px;
    }

    &:hover {
      background: darken($color: $c-accent, $amount: 7%);
    }

    .btn__icon {
      fill: $c-white
    }
  }

  &--admin-sm {
    min-width: auto;
    padding: 0 30px;

    @include xl {
      min-width: auto;
      padding: 0 35px;
    }
  }

  &--circle {
    &::before {
      content: '';
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: $c-white;
      flex-shrink: 0;
      margin: 0 10px 0 0;
    }
  }

  &--mt60 {
    margin-top: 30px;

    @include xs {
      margin: 15px 0 0;
    }
  }

  &__icon {
    width: 7px;
    height: 12px;
    margin: 0 0 0 10px;
    flex-shrink: 0;
    transition: fill .3s;

    &--admin {
      width: 24px;
      height: 24px;
      margin: 0 10px 0 0;
    }
  }

  &.disabled,
  &:disabled {
    background: $c-border;
    color: $c-white;
    pointer-events: none;
  }

}
