.modal--course .modal__content {
  max-width: 1255px;
  width: 100%;
}

.modal-course {
  background: $c-white;
  padding: 60px;
  border-radius: 30px;

  @include xl {
    padding: 30px;
  }

  @include xs {
    padding: 15px 15px 25px;
  }

  &__title {
    font-weight: 500;
    font-size: 52px;
    margin: 0 60px 30px 0;

    @include xl {
      font-size: 32px;
      margin: 0 60px 35px 0;
    }

    @include lg {
      font-size: 28px;
      margin: 0 35px 30px 0;
    }

    @include xs {
      font-size: 24px;
    }
  }

  &__info {
    display: flex;
    align-items: center;
    margin: 0 0 30px;

    @include xl {
      margin: 0 0 40px;
    }

    @include md {
      display: block;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__info-text {
    margin: 0 30px 0 0;

    p {
      &:not(:last-of-type) {
        margin: 0 0 16px;
      }
    }

    @include xl {
      margin: 0 140px 0 0;
    }

    @include lg {
      margin: 0 30px 0 0;
    }

    @include md {
      margin: 0 0 30px;
    }

    @include xs {
      font-size: 12px;
    }
  }

  &__info-features {
    flex: 0 0 475px;

    @include xl {
      flex: 0 0 355px;
    }

    .course-features__item::before {
      background: $c-accent;
    }
  }

  &__list-title {
    font-weight: 500;
    font-size: 24px;
    margin: 0 0 15px;

    @include xl {
      font-size: 20px;
    }

    @include xs {
      font-size: 16px;
    }
  }

  &__list {
    margin: 0 0 60px 6px;

    li {
      display: flex;

      &::before {
        content: '';
        width: 16px;
        height: 16px;
        background: url('../img/svg/arrow-right-circle.svg') no-repeat center/cover;
        flex-shrink: 0;
        margin: 3px 10px 0 0;

        @include xl {
          margin: -1px 10px 0 0;
        }
      }

      &:not(:last-of-type) {
        margin: 0 0 20px;
      }
    }

    @include xl {
      font-size: 12px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include xs {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__price {
    font-weight: 500;
    font-size: 72px;
    margin: 0 20px 0 0;

    @include xl {
      font-size: 52px;
    }

    @include sm {
      font-size: 42px;
    }

    @include xs {
      font-size: 36px;
      margin: 0 0 30px;
    }
  }

  &__btn {
    min-height: 90px;

    @include xl {
      min-width: 250px;
      min-height: 60px;
    }

    @include xs {
      min-width: 100%;
    }
  }
}
