.review-video {

  &__cover {
    position: relative;
    display: block;
    height: 320px;
    margin: 0 0 15px;
    border-radius: 15px;
    outline: none;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 15px;
      background: linear-gradient(0deg, rgba(53, 53, 53, 0.2), rgba(53, 53, 53, 0.2));
    }

    @include xl {
      height: 220px;
    }

    @include xs {
      height: 190px;
    }

    img {
      width: 100%;
      height: 100%;
      border-radius: 15px;
      object-fit: cover;
    }
  }
}
