.footer {
  background: $c-dark;
  padding: 30px 0;

  @include xs {
    padding: 16px 0;
  }

  &__wrap {
    display: flex;
    justify-content: space-between;

    @include md {
      display: block;
    }
  }

  &__logo {
    display: inline-flex;
    align-items: center;
    flex-shrink: 0;
    font-family: $font-family-alegreya;
    font-weight: 700;
    font-size: 24px;
    color: $c-white;
    margin: 0 20px 0 0;

    @include md {
      margin: 0 0 15px;
    }

    @include xs {
      display: none;
    }
  }

  &__logo-icon {
    width: 28px;
    height: 23px;
    fill: $c-white;
    margin: -5px 16px 0 0;
    flex-shrink: 0;
  }

  &__info {}

  &__menu {
    margin: 0 0 30px;

    @include md {
      margin: 0 0 10px;
    }

    @include xs {
      margin: 0 0 30px;
    }
  }

  &__menu-list {
    display: flex;

    @include xs {
      display: block;
    }
  }

  &__nav-item {
    &:not(:last-of-type) {
      margin: 0 60px 0 0;

      @include md {
        margin: 0 30px 0 0;
      }

      @include xs {
        border-bottom: 1px solid #373737;
        padding: 0 0 25px;
        margin: 0 0 25px;
      }
    }
  }

  &__nav-link {
    color: $c-white;
    transition: color .3s;

    &:hover:not(.active) {
      color: $c-accent;
    }

    @include xs {
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 16px;
      border-bottom: none;
      padding: 0;

      &::after {
        content: '';
        border: 2px solid $c-white;
        width: 8px;
        height: 8px;
        flex-shrink: 0;
        margin: 0 10px 0 20px;
        border-bottom: none;
        border-left: none;
        transform: rotate(45deg);
      }
    }
  }

  &__info-wrap {
    display: flex;
    justify-content: flex-end;

    @include md {
      flex-direction: column;
      justify-content: flex-start;
    }

    @include xs {
      border-top: 1px solid #373737;
      margin: 0 -15px;
    }
  }

  &__soc {
    display: flex;

    @include md {
      display: none;
    }
  }

  &__soc-item {
    &:not(:last-of-type) {
      margin: 0 15px 0 0;
    }
  }

  &__soc-link {
    display: block;
    width: 14px;
    height: 14px;

    &:hover {
      .footer__soc-icon {
        fill: $c-accent;
      }
    }
  }

  &__soc-icon {
    width: 100%;
    height: 100%;
    fill: $c-white;
    transition: fill .3s;
  }

  &__info-link {
    font-weight: 500;
    font-size: 14px;
    color: $c-white;
    transition: color .3s;
    margin: 0 0 0 30px;

    @include md {
      margin: 0;
      padding: 5px 0;
    }

    @include xs {
      text-align: center;
      padding: 15px 0;
      border-bottom: 1px solid #373737;
    }

    &:hover {
      color: $c-accent;
    }
  }
}
