.modal--comment .modal__content {
  max-width: 1255px;
  width: 100%;
}

.modal-comment {
  background: $c-white;
  padding: 60px;
  border-radius: 30px;

  @include xl {
    padding: 30px;
  }

  @include xs {
    padding: 15px 15px 25px;
  }

  &__title {
    font-weight: 500;
    font-size: 52px;
    margin: 0 60px 15px 0;

    @include xl {
      font-size: 32px;
      margin: 0 60px 15px 0;
    }

    @include lg {
      font-size: 28px;
      margin: 0 35px 15px 0;
    }

    @include xs {
      font-size: 24px;
    }
  }

  &__subtitle {
    margin: 0 0 30px;

    @include xs {
      font-size: 12px;
    }
  }

  &__msg {
    position: relative;
    margin: 0 0 30px;
  }

  &__textarea {
    width: 100%;
    height: 400px;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 15px 15px 40px;

    @include xs {
      padding: 15px 15px 25px;
    }
  }

  &__counter {
    position: absolute;
    right: 20px;
    bottom: 16px;
    font-size: 16px;
    color: rgba(53, 53, 53, 0.5);

    @include xs {
      right: 6px;
      bottom: 7px;
      font-size: 12px;
    }
  }

  &__btn {
    @include xl {
      min-width: 250px;
      min-height: 60px;
    }

    @include xs {
      min-width: 100%;
    }
  }
}
