.modal--enter .modal__content,
.modal--password .modal__content {
  max-width: 1040px;
  width: 100%;
}

.modal--enter .modal__close-btn,
.modal--password .modal__close-btn {
  width: 24px;
  height: 24px;
  top: 15px;
  right: 15px;
}

.modal--enter .modal__close-btn::before,
.modal--password .modal__close-btn::before {
  height: 1px;
  top: 11px;
}

.modal--enter .modal__close-btn::after,
.modal--password .modal__close-btn::after {
  height: 1px;
  bottom: 12px;
}

.modal-enter {
  display: flex;
  background: $c-white;
  border-radius: 20px;
  padding: 30px 60px 30px 30px;

  @include lg {
    padding: 30px;
  }

  &__cover {
    position: relative;
    display: flex;
    flex-direction: column;
    flex: 0 1 420px;
    margin: 0 40px 0 0;
    border-radius: 10px;
    overflow: hidden;

    @include md {
      display: none;
    }
  }

  &__logo {
    width: 146px;
    height: 117px;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 1;
    transform: translate(-50%, -50%);
  }

  &__cover-img {
    flex-grow: 1;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
        linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
        linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
        linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)),
        linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2));
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__cover-link {
    background: $c-dark;
    text-align: center;
    padding: 40px 20px 45px;

    a {
      font-family: $font-family-alegreya;
      font-weight: 700;
      font-size: 18px;
      color: $c-white;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    flex: 0 0 490px;

    @include md {
      flex: 1 1 auto;
    }
  }

  &__tabs {
    margin: 0 0 40px;

    @include xl {
      margin: 0 0 10px;
    }

    @include md {
      margin: 0;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 36px;
    letter-spacing: 0.04em;
    margin: 0 0 60px;

    @include md {
      margin: 0 0 30px;
    }

    @include md {
      font-size: 26px;
    }

    @include xs {
      font-size: 20px;
    }

  }

  &__tabs-item {
    margin: 0;
    padding: 0 0 5px;
    transition: color .3s;

    &:hover:not(.active) {
      color: $c-accent;
    }

    &:first-of-type {
      margin-right: 60px;

      @include md {
        margin-right: 45px;
      }
    }

    &.active {
      border-bottom: 2px solid $c-accent;
    }
  }

  &__form {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    &--tab {
      display: none;

      &.active {
        display: flex;
        animation: faded 1.5s;
      }
    }

  }

  &__label {
    margin: 0 0 35px;

    @include md {
      margin: 0 0 30px;
    }
  }

  &__form-item {
    margin: 0 0 80px;

    @include xl {
      margin: 0 0 50px;
    }

    @include md {
      margin: 0 0 35px;
    }
  }

  &__form-footer {
    margin: auto 0 0;
  }

  &__form-btn {
    min-width: 100%;
    min-height: 90px;

    @include xl {
      min-width: 100%;
      min-height: 90px;
    }

    @include md {
      min-height: 60px;
    }

    &:last-of-type {
      margin: 30px 0 0;

      @include md {
        margin: 15px 0 0;
      }
    }
  }

  &__form-forgot {
    font-size: 16px;
    color: $c-label;
    text-align: center;
    margin: 20px 0 0;

    @include md {
      margin: 15px 0 0;
    }

    a {
      color: $c-text;
      transition: color .3s;

      &:hover {
        color: $c-accent;
      }
    }
  }
}
