.about-partners {

  &__head {
    max-width: 870px;
    width: 100%;

    @include xl {
      max-width: 690px;
    }
  }

  &__wrap {
    display: flex;
    flex-wrap: wrap;
    margin: 0 0 -15px;

    @include sm {
      justify-content: space-between;
    }
  }

  &__item {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 200px;
    width: calc(20% - 15px * 4 / 5);
    background: $c-substrate;
    border-radius: 5px;
    margin: 0 15px 15px 0;

    &:nth-child(5n) {
      margin-right: 0;

      @include lg {
        margin-right: 15px;
      }

      @include sm {
        margin-right: 0;
      }
    }

    @include lg {
      width: calc(33.33% - 15px * 2 / 3);

      &:nth-child(3n) {
        margin-right: 0;
      }
    }

    @include sm {
      width: calc(50% - 15px / 2);
      margin: 0 0 15px;
    }

    @include xs {
      width: 100%;
    }

    img {
      max-width: 186px;
      max-height: 160px;
      object-fit: cover;
    }
  }
}
