.review-user {
  display: flex;
  align-items: center;

  &__photo {
    flex-shrink: 0;
    width: 32px;
    height: 32px;
    margin: 0 10px 0 0;

    @include xl {
      margin: 0 15px 0 0;
    }

    @include xs {
      margin: 0 10px 0 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &__info {}

  &__name {
    font-size: 18px;

    @include xs {
      font-size: 16px;
    }
  }

  &__link {
    font-size: 13px;
    color: $c-label;
    transition: color .3s;

    &:hover {
      color: $c-accent;
    }
  }
}
