.reminder {

  &__item {
    background: $c-substrate;
    border-radius: 5px;

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }

    &.open {
      .reminder__content {
        opacity: 1;
        box-sizing: content-box;
        padding-bottom: 40px;
        margin: -15px 0 0;

        @include xl {
          padding-bottom: 30px;
        }

        @include xs {
          margin: 0;
          padding-bottom: 15px;
        }
      }
    }
  }

  &__head {
    width: 100%;
    font-weight: 500;
    font-size: 24px;
    text-align: left;
    padding: 30px;

    @include xl {
      font-size: 18px;
    }

    @include xs {
      font-size: 14px;
      padding: 15px;
    }
  }

  &__content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: all 0.3s ease-out;
    padding: 0 30px;

    @include xs {
      font-size: 12px;
      padding: 0 15px;
    }
  }
}
