.courses-cabinet {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 0 0 90px;

  @include xl {
    .cabinet__head {
      margin: 0 0 150px;
    }
  }

  @include lg {
    .cabinet__head {
      margin: 0 0 45px;
    }
  }

  @include xs {
    padding: 0 0 60px;
  }

  .course-card {
    background: $c-substrate;
  }

  &__wrap {
    margin: 0 0 60px;
    padding: 0 410px 0 0;

    @include xl {
      padding: 0;
      margin: 0 0 45px;
    }

    @include lg {
      display: flex;
      flex-direction: column;
      order: 1;
      margin: 60px 0 0;
    }
  }

  &__user {
    position: absolute;
    top: 30px;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 350px;
    background: $c-white;
    box-shadow: 0px 9px 30px rgba(176, 190, 197, 0.1), 0px 4px 5px rgba(176, 190, 197, 0.3);
    border-radius: 5px;
    padding: 50px 20px 20px;

    @include xl {
      width: 255px;
      padding: 20px;
    }

    @include lg {
      position: static;
      top: auto;
      right: auto;
      align-self: center;
      margin: 0 0 30px;
    }

    @include xs {
      width: 100%;
    }
  }

  &__user-photo {
    width: 140px;
    height: 140px;
    margin: 0 0 20px;

    @include xl {
      width: 100px;
      height: 100px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
    }
  }

  &__user-name {
    font-weight: 500;
    font-size: 24px;
    margin: 0 0 10px;
  }

  &__user-experience {
    font-size: 18px;
  }

  &__user-experience-val {
    color: $c-secondary;
  }

  &__stat {}
}
