.checkbox {
  font-size: 16px;
  cursor: pointer;

  @include xs {
    font-size: 12px;
  }

  &:hover {
    .checkbox__el {
      border-color: $c-accent;
    }
  }

  input {
    &:checked+.checkbox__wrap .checkbox__el::after {
      opacity: 1;
    }
  }

  &__wrap {
    display: flex;
  }

  &__el {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    border: 1px solid $c-text;
    border-radius: 3px;
    flex-shrink: 0;
    margin: 0 10px 0 0;
    transition: border .3s;

    @include xs {
      width: 24px;
      height: 24px;
      margin: -5px 10px 0 0;
    }

    &::after {
      content: '';
      width: 12px;
      height: 12px;
      border-radius: 2px;
      opacity: 0;
      background: $c-accent;
      transition: opacity .3s;

      @include xs {
        width: 16px;
        height: 16px;
      }
    }
  }
}
