@mixin retina {

  @media (min-resolution: $retina-dpi),
  (min-resolution: $retina-dppx) {
    @content;
  }
}

// font properties mixins
@mixin title-h1 {
  font-weight: 500;
  font-size: 70px;
}

@mixin title-h2 {
  font-weight: 500;
  font-size: 48px;
}

@mixin important-text {
  font-size: 26px;
}

@mixin label-text {
  font-size: 14px;
}

// Desktop first

@mixin xl {
  @media (max-width: $viewport--xl - 1) {
    @content;
  }
}

@mixin lg {
  @media (max-width: $viewport--lg - 1) {
    @content;
  }
}

@mixin md {
  @media (max-width: $viewport--md - 1) {
    @content;
  }
}

@mixin sm {
  @media (max-width: $viewport--sm - 1) {
    @content;
  }
}

@mixin xs {
  @media (max-width: $viewport--xs - 1) {
    @content;
  }
}
