.course-page {
  &__wrap {
    display: flex;

    @include lg {
      display: block;
    }
  }

  &__aside {
    position: relative;
    flex: 0 0 460px;
    margin: 0 30px 0 0;

    @include xl {
      flex: 0 0 300px;
    }

    @include lg {
      margin: 0 0 15px;
    }
  }

  &__aside-wrap {
    position: sticky;
    top: 30px;
    width: 100%;
    height: 100vh;
    padding: 0 0 30px;
    overflow-y: auto;

    @include lg {
      position: static;
      top: auto;
      height: auto;
      padding: 0;
    }
  }

  &__aside-item {
    background: $c-white;
    border-radius: 10px;
    padding: 10px;

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }

    &.open {
      .course-page__aside-item-head-icon {
        transform: rotate(-180deg);
      }

      .course-page__aside-item-list {
        opacity: 1;
        box-sizing: content-box;
        padding-top: 15px;
      }
    }
  }

  &__aside-item-head {
    min-height: 60px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 24px;
    background: $c-white;
    text-align: left;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 12px 24px;
    transition: color .3s;

    &:hover {
      color: $c-accent;
    }

    @include xl {
      font-size: 18px;
    }
  }

  &__aside-item-head-icon {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
    margin: 0 0 0 20px;
    transform: rotate(0);
    transition: transform .3s;
  }

  &__aside-item-list {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    will-change: max-height;
    transition: all 0.3s ease-out;
  }

  &__aside-item-list-item {

    &:not(:last-of-type) {
      margin: 0 0 15px;
    }
  }

  &__aside-item-list-item-link {
    display: flex;
    min-height: 90px;
    background: $c-white;
    border: 1px solid $c-border;
    border-radius: 5px;
    padding: 15px 20px;
    color: $c-text;
    transition: background .3s, border .3s;

    &:hover {
      border-color: transparent;
      background: $c-substrate;
    }

    &.active {
      border-color: transparent;
      background: $c-substrate;

      .course-page__aside-item-list-item-circle {
        background: $c-secondary;
      }
    }

    @include xl {
      min-height: 78px;
    }
  }

  &__aside-item-list-item-icon {
    width: 18px;
    height: 18px;
    margin: 0 15px 0 0;
    flex-shrink: 0;
  }

  &__aside-item-list-item-wrap {
    margin: 0 20px 0 0;
  }

  &__aside-item-list-item-name {
    font-weight: 500;
    font-size: 20px;
    margin: 0 0 10px;

    @include xl {
      font-size: 14px;
    }
  }

  &__aside-item-list-item-descr {
    font-size: 16px;

    @include xl {
      font-size: 12px;
    }
  }

  &__aside-item-list-item-circle {
    width: 16px;
    height: 16px;
    align-self: center;
    flex-shrink: 0;
    border-radius: 50%;
    border: 1px solid $c-text;
    margin: 0 0 0 auto;
    transition: background .3s, border .3s;
  }

  &__content {
    flex: 1 1 auto;
  }

  &__content-item {
    background: $c-white;
    border-radius: 5px;
    padding: 60px;

    &:not(:last-of-type) {
      margin: 0 0 30px;
    }

    @include lg {
      padding: 40px;
    }

    @include md {
      padding: 30px;
    }

    @include sm {
      padding: 20px;
    }

    @include xs {
      padding: 15px;
    }
  }

  &__content-intro {
    .course-page__content-title--sm {
      @include xl {
        font-size: 24px;
      }

      @include md {
        font-size: 20px;
      }

      @include xs {
        font-size: 16px;
      }
    }
  }

  &__content-title {
    font-weight: 500;
    font-size: 36px;
    margin: 0 0 30px;

    @include xl {
      font-size: 32px;
    }

    @include md {
      font-size: 26px;
    }

    @include xs {
      font-size: 20px;
    }

    &--sm {
      font-size: 24px;
      margin: 0 0 15px;

      @include xl {
        font-size: 20px;
      }

      @include md {
        font-size: 18px;
      }

      @include xs {
        font-size: 14px;
      }
    }
  }

  &__content-intro-text {
    margin: 0 0 60px;

    @include xl {
      margin: 0 0 30px;
    }

    @include xs {
      font-size: 12px;
    }
  }


  &__content-intro-list {

    li {
      display: flex;

      &::before {
        content: '';
        width: 16px;
        height: 16px;
        background: url('../img/svg/arrow-right-circle.svg') no-repeat center/cover;
        flex-shrink: 0;
        margin: 3px 10px 0 0;

        @include xl {
          margin: -1px 10px 0 0;
        }
      }

      &:not(:last-of-type) {
        margin: 0 0 20px;
      }
    }

    @include xl {
      font-size: 14px;
    }

    @include xs {
      font-size: 12px;
    }
  }

  &__content-item-table {
    border: 1px solid $c-border;
    box-shadow: 0px 2px 4px rgba(4, 38, 82, 0.06);
    border-radius: 10px;

    @include sm {
      overflow-x: auto;
    }

    table {
      width: 100%;
      border-collapse: collapse;

      @include sm {
        width: 800px;
      }
    }

    tr {
      &:last-of-type {
        td {
          border-bottom: none;

          &:first-of-type {
            border-left: none;
          }
        }
      }
    }

    th {
      font-weight: 700;
      font-size: 14px;
      padding: 12px 16px;
      border-bottom: 2px solid $c-secondary;

      @include xl {
        font-weight: 400;
        font-size: 12px;
      }
    }

    td {
      font-size: 18px;
      border: 1px solid $c-border;
      padding: 16px 12px;

      &:first-of-type {
        @include sm {
          min-width: 50px;
        }
      }

      &:first-of-type,
      &:last-of-type {
        font-size: 14px;
      }

      &:first-of-type,
      &:nth-child(3) {
        text-align: center;
      }

      &:last-of-type {
        vertical-align: top;
        border-right: none;

        @include xl {
          max-width: 200px;
        }

        @include sm {
          font-size: 12px;
        }
      }

      @include sm {
        min-width: 110px;
        font-size: 12px;
        padding: 6px 10px;

        &:nth-child(2) {
          text-align: center;
        }
      }
    }
  }

  &__content-item-table-grey {
    font-size: 14px;
    letter-spacing: -0.2px;
    color: $c-label;
    margin: 4px 0 0;

    @include sm {
      font-size: 12px;
    }
  }

  &__content-video {}

  &__content-video-content {
    width: 100%;
    height: 535px;
    margin: 0 0 30px;

    @include xl {
      height: 380px;
    }

    @include xs {
      height: 250px;
      margin: 0 0 15px;
    }

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 10px;
    }
  }

  &__content-video-descr {
    font-size: 18px;

    @include xs {
      font-size: 12px;
    }

    &.active {
      .course-page__content-video-descr-text {
        overflow: auto;
        display: block;
        -webkit-line-clamp: inherit;
      }
    }
  }

  &__content-video-descr-text {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
  }

  &__content-video-descr-btn {
    font-size: 18px;
    color: $c-secondary;
    text-decoration: underline;
    transition: color .3s;

    @include xs {
      font-size: 16px;
      margin: 10px 0 0;
    }

    &:hover {
      color: $c-accent;
      text-decoration: none;
    }
  }

  &__content-text {
    h3 {
      &:not(:first-of-type) {
        margin-top: 30px;
      }
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 20px;

        @include xl {
          margin: 0 0 15px;
        }
      }

      @include xs {
        font-size: 12px;
      }
    }
  }

  &__content-audio {}

  &__content-audio-item {
    display: flex;
    align-items: center;

    &:not(:last-of-type) {
      margin: 0 0 30px;
    }

    audio {
      width: 273px;
      background: $c-substrate;
      border-radius: 10px;
      margin: 0 15px 0 0;

      @include sm {
        width: 100%;
        margin: 0 0 15px;
      }
    }

    @include sm {
      display: block;
    }

    @include xs {
      font-size: 12px;
    }
  }

  &__content-audio-word {
    font-size: 18px;
    background: $c-substrate;
    border-radius: 10px;
    padding: 15px;
    flex-shrink: 0;
    margin: 0 15px 0 0;

    @include sm {
      margin: 0 0 15px;
    }
  }

  &__content-audio-word-item {
    font-size: 18px;

    &:nth-child(2) {
      margin: 0 13px;

      &::before,
      &::after {
        content: '|';
        margin: 0 5px;
      }

    }
  }

  &__content-reading {}

  &__content-reading-content {
    border-bottom: 1px solid $c-text;
    margin: 0 0 30px;
  }

  &__content-reading-item {
    margin: 0 0 30px;

    @include xs {
      font-size: 12px;
    }

    p {
      &:not(:last-of-type) {
        font-size: 16px;
        margin: 0 0 15px;
      }
    }

  }

  &__content-reading-footer {
    @include xs {
      font-size: 12px;
    }
  }

  &__content-practice {}

  &__content-practice-subtitle {
    margin: -15px 0 30px;
  }

  &__content-practice-audio {
    width: 100%;
    background: $c-substrate;
    border-radius: 10px;
    margin: 0 0 30px;
  }

  &__content-practice-wrap {}

  &__content-practice-item {
    &:not(:last-of-type) {
      margin: 0 0 30px;
    }
  }

  &__content-practice-question {
    font-weight: 500;
    font-size: 18px;
    color: rgba($color: $c-text, $alpha: .9);
    margin: 0 0 15px;

    @include xs {
      font-size: 14px;
    }
  }

  &__content-practice-answers {
    display: inline-block;
    column-count: 2;

    @include sm {
      display: block;
      column-count: auto;
    }
  }

  &__content-practice-answers-item {
    overflow: hidden;
    page-break-inside: avoid;
    break-inside: avoid-column;
    margin: 0 15px 15px 0;

    @include sm {
      overflow: auto;
      margin: 0 0 15px;
    }
  }

  &__content-practice-answers-btn {
    display: flex;
    align-items: center;
    min-width: 175px;
    min-height: 30px;
    background: $c-substrate;
    border-radius: 5px;
    padding: 4px 9px;
    transition: color .3s;

    &:hover {
      color: $c-accent;
    }

    @include xl {
      font-size: 14px;
    }

    @include sm {
      min-height: 50px;
    }
  }

  &__content-practice-answers-option {
    font-size: 18px;
    border-right: 1px solid $c-border;
    padding: 0 5px 0 0;
    margin: 0 20px 0 0;
  }

  &__btn-up {
    display: none;

    @include xs {
      min-width: 100%;
      min-height: 60px;
      display: flex;
      margin: 45px 0 0;
    }
  }
}
