.modal--success .modal__content {
  max-width: 462px;
  width: 100%;
}

.modal--success .modal__close-btn {
  width: 24px;
  height: 24px;
  top: 15px;
  right: 15px;
}

.modal--success .modal__close-btn::before {
  height: 1px;
  top: 11px;
}

.modal--success .modal__close-btn::after {
  height: 1px;
  bottom: 12px;
}

.modal-success {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background: $c-white;
  border-radius: 20px;
  padding: 30px;

  &__icon {
    width: 90px;
    height: 90px;
    margin: 0 0 60px;

    @include md {
      margin: 0 0 30px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 36px;
    margin: 0 0 20px;
  }

  &__subtitle {
    max-width: 378px;
    width: 100%;
    margin: 0 0 60px;

    @include md {
      margin: 0 0 30px;
    }
  }

  &__mail {
    font-size: 24px;
    color: $c-accent;
    margin: 0 0 60px;

    @include md {
      margin: 0 0 30px;
    }
  }

  &__btn {
    min-width: 100%;
    min-height: 60px;

    @include xl {
      min-height: 60px;
      min-width: 100%;
    }
  }
}
