.files {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -7px -15px;

  @include xl {
    margin: 0 -2px -15px;
  }

  @include lg {
    margin: 0 -5px -15px;
  }

  &__item {
    flex: 0 1 16.66%;
    padding: 0 7px 15px;

    @include xl {
      padding: 0 2px 15px;
    }


    @include lg {
      flex: 0 1 20%;
      padding: 0 5px 15px;
    }

    @include md {
      flex: 0 1 25%;
    }

    @include sm {
      flex: 0 1 33.33%;
    }

    @include xs {
      flex: 0 1 50%;
    }
  }

  &__item-link {
    min-height: 180px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: $c-substrate;
    border-radius: 15px;
    padding: 20px;

    @include xl {
      min-height: 120px;
    }
  }

  &__item-icon {
    width: 55px;
    height: 55px;
    object-fit: cover;
    margin: 0 0 15px;

    @include md {
      width: 32px;
      height: 32px;
    }
  }

  &__item-text {
    font-size: 18px;
    color: rgba($color: $c-text, $alpha: .6);

    @include md {
      font-size: 14px;
    }

    @include xs {
      font-size: 12px;
    }
  }
}
