.home-hero {
  position: relative;
  min-height: 920px;
  padding: 200px 0 100px;

  @include xl {
    min-height: 630px;
    padding: 160px 0 100px;
  }

  @include md {
    min-height: auto;
    padding: 110px 0 60px;
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    background: $c-secondary;
  }

  &__grid {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @include md {
      display: none;
    }
  }

  &__iroglyphs {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    max-width: 1027px;
    width: 100%;
    height: auto;
    object-fit: cover;

    @media (max-width: 1730px) {
      max-width: 710px;
    }

    @include lg {
      max-width: 510px;
    }

    @include md {
      display: none;
    }
  }

  &__wrap {
    max-width: 1000px;
    width: 100%;

    @include xl {
      max-width: 628px;
    }
  }

  &__course {
    display: inline-flex;
    align-items: center;
    font-size: 12px;
    padding: 6px 23px 6px 8px;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 38px;
    margin: 0 0 15px;

    @include xl {
      padding: 3px 13px 3px 6px;
    }

    @include md {
      padding: 6px 14px 6px 8px;
    }

    @include xs {
      display: flex;
      max-width: 330px;
      width: 100%;
      margin: 0 auto 15px;
    }
  }

  &__course-tag {
    min-height: 26px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    color: $c-accent;
    background: $c-secondary;
    padding: 0 10px;
    flex-shrink: 0;
    border-radius: inherit;
    margin: 0 10px 0 0;

    @include xl {
      padding: 0 7px;
    }

    @include md {
      padding: 0 10px;
    }
  }

  &__title {
    font-size: 76px;
    font-weight: 500;
    line-height: 1.1;
    margin: 0 0 33px;

    @include xl {
      font-size: 50px;
      margin: 0 0 15px;
    }

    @include xs {
      font-size: 34px;
    }
  }

  &__subtitle {
    max-width: 706px;
    width: 100%;
    margin: 0 0 60px;

    @include xl {
      max-width: 414px;
    }

    @include xs {
      font-size: 16px;
      margin: 0 0 30px;
    }
  }

  &__btn {
    min-height: 90px;

    @include xl {
      min-height: 60px;
    }

    @include xs {
      min-width: 100%;
    }
  }
}
