.home-promotion {

  .container {
    position: relative;
  }

  &__head {
    padding: 60px 0 300px;
    color: $c-white;
    background: $c-accent;

    @include xl {
      padding: 45px 0 160px;
    }

    @include xs {
      padding: 30px 0 350px;
    }

    .landing-section__head {
      max-width: 1118px;
      width: 100%;

      @include xl {
        max-width: 770px;
      }

      @include md {
        margin: 0 0 30px;
      }

      .landing-section__tag {
        border-color: $c-white;
      }

      .landing-section__subtitle {
        max-width: 886px;
        width: 100%;

        @include xl {
          max-width: 568px;
        }
      }
    }
  }

  &__nav {
    position: absolute;
    bottom: -41px;
    right: 25px;
    display: flex;
    flex-shrink: 0;

    @include md {
      position: static;
      bottom: auto;
      right: auto;
    }
  }

  &__nav-btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 82px;
    height: 82px;
    background: $c-white;
    border-radius: 50%;

    &:hover {
      .home-promotion__nav-btn-icon {
        fill: $c-accent;
      }
    }

    @include xl {
      width: 55px;
      height: 55px;
    }

    @include md {
      width: 60px;
      height: 60px;
    }
  }

  &__nav-btn-prev {
    margin: 0 10px 0 0;

    @include md {
      margin: 0 15px 0 0;
    }

    .home-promotion__nav-btn-icon {
      transform: rotate(-180deg);
    }
  }

  &__nav-btn-icon {
    width: 12px;
    height: 20px;
    fill: $c-text;
    transition: fill .3s;

    @include xl {
      width: 8px;
      height: 13px;
    }

    @include md {
      width: 9px;
      height: 15px;
    }
  }

  &__nav-btn-next {}

  &__slider {
    margin: -280px 0 0;

    @include xl {
      margin: -140px 0 0;
    }

    @include xs {
      margin: -330px 0 0;
    }

    .swiper-slide {
      width: 100%;
      max-width: 1680px;

      @media (max-width: 1700px) {
        max-width: 1240px;

        .promotion__bg {
          width: auto;
        }
      }

      @include xl {
        .promotion__bg {
          width: 49%;
        }
      }

      @media (max-width: 1250px) {
        padding: 0 25px;
      }

      @include xs {
        padding: 0 15px;

        .promotion__bg {
          width: auto;
        }
      }
    }
  }

  &__item {
    padding: 60px;
    border-radius: 30px;
    overflow: hidden;

    @include xl {
      padding: 40px;
    }

    @include md {
      padding: 32px 25px 30px 20px;
    }
  }
}
