.reviews {
  &__tabs {
    text-align: right;
    margin: -60px 0 30px;

    @include xl {
      margin: -45px 0 15px;
    }

    @include lg {
      margin: 0 0 15px;
    }

    @include xs {
      text-align: left;
    }
  }

  &__tabs-wrap {
    display: inline-flex;
    background: $c-substrate;
    border-radius: 10px;
    padding: 5px;
    filter: drop-shadow(0px 9px 30px rgba(176, 190, 197, 0.1)) drop-shadow(0px 4px 5px rgba(176, 190, 197, 0.3));

    @include xs {
      width: 100%;
    }
  }

  &__tabs-item {
    min-width: 103px;
    min-height: 40px;
    border-radius: 10px;
    padding: 4px 10px;
    transition: color .3s;

    &:hover:not(.active) {
      color: $c-accent;
    }

    &:first-of-type {
      margin: 0 5px 0 0;
    }

    &.active {
      background: $c-white;
    }

    @include xl {
      font-size: 16px;
      min-height: 30px;
    }

    @include xs {
      flex: 0 1 50%;
    }
  }

  &__content {
    display: none;

    @include lg {
      .review-video__cover {
        height: 300px;
      }
    }

    @include xs {
      .review-video__cover {
        height: 220px;
      }
    }

    &.active {
      display: flex;
      animation: faded 1.5s;
    }
  }
}
