.final-comment {
  padding: 130px 0 120px;

  @include xl {
    padding: 130px 0 90px;
  }

  @include xs {
    padding: 75px 0 60px;
  }

  &__back {
    display: inline-block;
    font-size: 18px;
    color: $c-text;
    margin: 0 0 30px;
    transition: color .3s;

    @include xs {
      font-size: 14px;
      margin: 0 0 15px;
    }

    &:hover {
      color: $c-accent;
    }
  }

  &__content {
    background: $c-white;
    border-radius: 15px;
    padding: 60px;

    @include xl {
      padding: 60px 100px 60px 60px;
    }

    @include md {
      padding: 25px;
    }

    @include xs {
      padding: 15px;
    }
  }

  &__title {
    font-weight: 500;
    font-size: 48px;
    margin: 0 0 30px;

    @include xl {
      font-size: 32px;
    }

    @include xs {
      font-size: 20px;
    }
  }

  &__content-text {
    font-size: 24px;
    color: rgba($color: $c-text, $alpha: .9);

    @include xl {
      font-size: 20px;
    }

    @include md {
      font-size: 16px;
    }

    @include xs {
      font-size: 12px;
    }

    p {
      &:not(:last-of-type) {
        margin: 0 0 24px;

        @include md {
          margin: 0 0 18px;
        }
      }
    }
  }

  &__content-text-item {
    &:not(:last-of-type) {
      margin: 0 0 60px;

      @include md {
        margin: 0 0 30px;
      }
    }
  }
}
